<template lang="en">
<div class="my-popups">
    <div class="modal d-block">
        <div class="modal-dialog modal-dialog-centered modal-lg">
            <div class="modal-content">
                <div class="modal-header theme-primary partition-full d-flex">
                    <h4 class="modal-title">{{ $t('BulkMove') }}</h4>
                    <button type="button" class="close" v-on:click="Close">
                        <em class="fa fa-times" aria-hidden="true"></em>
                    </button>
                    <span class="user-guide">
                        <a class="ancuserguide text-white" data-toggle="collapse" href="#collapseBulkMove" role="button" aria-expanded="false" aria-controls="collapseExample">
                            <em class="fa fa-files-o pr-2"></em>{{ $t('GuideForUser') }}
                        </a>
                        <div id="collapseBulkMove" class="divancuserguide collapse overflow-auto">
                            <div id="scrollbarBulkMove" class="custom-scrollbar-js">
                                <div class="con" v-html="$t('User_Guide_Bulk_move_line_item')"></div>
                            </div>
                        </div>
                    </span>
                </div>

                <div class="modal-body">
                    <dynamic-form ref="bulkMoveForm" :buttons="buttons" :schema="formSchema" v-on:OnSubmit="onSubmit" lang="en"></dynamic-form>
                    <small class="text-danger">{{ $t('MandatoryString') }}</small>
                </div>
            </div>
        </div>
    </div>

    <!-- Confirmation Dialog -->
    <transition name="modal">
      <div class="my-popups" v-if="showConfirmationDialog">
        <div class="modal d-block dialog">
          <div class="modal-dialog modal-lg">
            <div class="modal-content">
              <div class="modal-header">
                <h4 class="modal-title">{{ $t('BulkMove') }}</h4>
                <button type="button" class="close" v-on:click="cancelConfirmation">
                  <em class="fa fa-times" aria-hidden="true"></em>
                </button>
              </div>
              <div class="modal-body">
                <div class="text-center">
                  <p>{{ confirmationMessage }}</p>
                </div>
              </div>
              <div class="modal-footer">
                <button @click="confirmBulkMove" class="btn btn-success">
                  <em class="fa fa-check pr-2" aria-hidden="true"></em> {{ $t('Yes') }}
                </button>
                <button @click="cancelConfirmation" class="btn btn-danger">
                  <em class="fa fa-times pr-2" aria-hidden="true"></em> {{ $t('No') }}
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </transition>

    <!-- Success Dialog -->
    <transition name="modal">
      <div class="my-popups" v-if="showSuccessDialog">
        <div class="modal d-block dialog">
          <div class="modal-dialog modal-lg">
            <div class="modal-content">
              <div class="modal-header">
                <h4 class="modal-title">{{ $t('BulkMove') }}</h4>
                <button type="button" class="close" v-on:click="closeSuccessDialog">
                  <em class="fa fa-times" aria-hidden="true"></em>
                </button>
              </div>
              <div class="modal-body">
                <div class="bg-f5f5f5 p-2 border-bottom">
                  <span>{{ $t('BulkMoveSuccessMessagePopUp') }}</span>
                </div>
                <div class="bg-f5f5f5 p-2">
                  <span>{{ successMessage }}</span>
                </div>
              </div>
              <div class="modal-footer">
                <button @click="closeSuccessDialog" class="btn btn-success">
                  <em class="fa fa-check pr-2" aria-hidden="true"></em> {{ $t('OK') }}
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </transition>

    <!-- Same Records Dialog -->
    <transition name="modal">
      <div class="my-popups" v-if="showSameRecordsDialog">
        <div class="modal d-block dialog">
          <div class="modal-dialog modal-xl">
            <div class="modal-content">
              <div class="modal-header">
                <h4 class="modal-title">{{ $t('BulkMove') }}</h4>
                <button type="button" class="close" v-on:click="closeSameRecordsDialog">
                  <em class="fa fa-times" aria-hidden="true"></em>
                </button>
              </div>
              <div class="modal-body">
                <div class="bg-f5f5f5 p-2 border-bottom">
                  <span class="text-danger">{{ sameRecordsMessage }}</span>
                </div>
                <div class="bg-f5f5f5 p-2">
                  <div class="table-responsive">
                    <table class="table table-bordered">
                      <thead class="thead-bg">
                        <tr>
                          <th>{{ $t('Client') }}</th>
                          <th>{{ $t('Utility') }}</th>
                          <th>{{ $t('TicketNumber') }}</th>
                          <th>{{ $t('ActivityId') }}</th>
                          <th>{{ $t('SBA') }}</th>
                          <th>{{ $t('Division') }}</th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr v-for="record in sameRecordsData" :key="record.id">
                          <td>{{ record.CONTRACT_NAME }}</td>
                          <td>{{ record.UTIL_ID }}</td>
                          <td>{{ record.TicketNumber }}</td>
                          <td>{{ record.activity_id }}</td>
                          <td>{{ record.SBA_DESC }}</td>
                          <td>{{ record.DIVISION_NAME }}</td>
                        </tr>
                      </tbody>
                    </table>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </transition>
</div>
</template>

<script>
import {
    locateBillingService
} from "../../../services/locateBillingService";

export default {
    name: "ChangeClientAndUtility",
    props: {
      requestFrom: {
        type: String,
        default: ''
      },
      ticketNumberList: {
        type: String,
        default: ''
      },
      activityIdList: {
        type: String,
        default: ''
      },
      oldClientList: {
        type: String,
        default: ''
      },
      oldUtilityList: {
        type: String,
        default: ''
      },
      workDateFrom: {
        type: String,
        default: ''
      },
      workDateTo: {
        type: String,
        default: ''
      },
      lineItemIds: {
        type: String,
        default: ''
      },
      BulkMoveBatchId: {
        type: String,
        default: ''
      },
    },
    data() {
        return {
            isLoading: false,
            formSchema: [{
                layoutType: "double",
                Data: [{
                        astype: "SearchableSelectField",
                        label: this.$t('CONTRACTNAME'),
                        name: "ClientName",
                        value: "",
                        placeholder: "",
                        visibility: true,
                        isSearch: false,
                        actionButtons: {
                            selectAll: true,
                            active: true,
                            inactive: true,
                            clear: true
                        },
                        config: {
                            options: [],
                            groupFields: ['groupText'],
                            isMultiple: false,
                            minSearchLength: 0,
                            statusFieldConfig: {
                                fieldName: 'status',
                                activeValues: ['Active', 'active'],
                                inactiveValues: ['Inactive', 'inactive'],
                                activeColor: '#28a745',
                                inactiveColor: '#dc3545'
                            },
                            onChange: this.onSelectionChange
                        },
                        validationRules: {
                            "required": true
                        },
                    },
                    {
                        astype: "SelectField",
                        label: "Utility Name",
                        name: "UtilityName",
                        visibility: true,
                        value: "",
                        placeholder: "Select utility",
                        config: {
                            options: [],
                            onChange: this.onSelectionUtilityChange
                        },
                        validationRules: {
                            "required": true
                        },
                    },
                    {
                        astype: "SelectField",
                        label: "SBA",
                        name: "SbaId",
                        visibility: true,
                        value: "",
                        placeholder: "Select SBA",
                        config: {
                            options: []
                        },
                        validationRules: {
                            "required": true
                        },
                    },
                    {
                        astype: "SelectField",
                        label: "Division",
                        name: "DivisionId",
                        visibility: true,
                        value: "",
                        placeholder: "Select division",
                        config: {
                            options: [],
                            onChange: this.onDivisionChange
                        },
                        validationRules: {
                            "required": true
                        },
                    },
                    {
                        astype: "SelectField",
                        label: "Invoice Number",
                        name: "InvoiceNumber",
                        visibility: true,
                        value: "",
                        placeholder: "Select invoice number",
                        config: {
                            options: []
                        },
                        validationRules: {
                            "required": false
                        },
                    },
                ],
            }],
            buttons: [{
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: "cancelForm",
                },
            ],
            showSuccessDialog: false,
          showSameRecordsDialog: false,
          showConfirmationDialog: false,
          successMessage: '',
          sameRecordsMessage: '',
          sameRecordsData: [],
          confirmationMessage: '',
          selectedClientIds: [],
          selectedClientNames: [],
          clientId: '',
          selectedFormData: null,
          SbaOptions: []
        };
    },
    created() {
        this.loadInitialData();
    },
    methods: {
         loadInitialData() {
             this.fetchClientList();
        },
        fetchClientList() {
            this.isLoading = true;
            locateBillingService.getClientList({
                    reqFrom: 'CUSTOM'
                })
                .then(response => {
                    this.isLoading = false;
                    if (response.data && response.data.items) {
                        const clientOptions = response.data.items.map(item => ({
                            description: null,
                            displayValue: item.text || item.CHILD_NAME,
                            groupText: item.groupText || item.PARENT_NAME,
                            groupValue: null,
                            metaData: null,
                            text: item.text || item.CHILD_NAME,
                            value: item.value || item.CHILD_USER_ID
                        }));
                        this.$refs.bulkMoveForm.updateProperty("ClientName", "config.options", clientOptions);
                        this.$refs.bulkMoveForm.UpdateKeyValue();
                    }
                })
                .catch(error => {
                    this.isLoading = false;
                    console.error('Error fetching client list:', error);
                });
        },
        onSelectionChange(value, x) {
            var vm = this;
            vm.clientId = x;
            if (x) {
                vm.isLoading = true;
                var url = `clientId=${x}&configId=&reqFrom=&clientIds=`;
                locateBillingService.getUtilityList(url).then(response => {
                    vm.isLoading = false;
                    let UtilityList = [];
                    if (response.data) {
                        var json = response.data;
                        if (json != null) {
                            json.forEach(item => {
                                let obj = {
                                    name: item.UTILITY_NAME,
                                    value: item.UTILITY_ID
                                };
                                UtilityList.push(obj);
                            });
                        }
                    }
                    // Clear dependent fields first
                    vm.clearDependentFields();
                    // Set new utility options
                    vm.$refs.bulkMoveForm.updateProperty("UtilityName", "config.options", UtilityList);
                    vm.$refs.bulkMoveForm.UpdateKeyValue();
                }).catch(error => {
                    vm.isLoading = false;
                    vm.clearDependentFields();
                    console.error('Error loading utilities:', error);
                });
            } else {
                // Client cleared, clear all dependent fields
                vm.clearDependentFields();
            }
        },
        async onSelectionUtilityChange(value, x) {
            var vm = this;
            if (x && x.value) {
                vm.UtilityId = x.value;
                // Clear SBA, Division, and Invoice Number options and values
                vm.$refs.bulkMoveForm.updateProperty("SbaId", "config.options", []);
                vm.$refs.bulkMoveForm.updateProperty("SbaId", "value", "");
                vm.$refs.bulkMoveForm.updateProperty("DivisionId", "config.options", []);
                vm.$refs.bulkMoveForm.updateProperty("DivisionId", "value", "");
                vm.$refs.bulkMoveForm.updateProperty("InvoiceNumber", "config.options", []);
                vm.$refs.bulkMoveForm.updateProperty("InvoiceNumber", "value", "");
                vm.$refs.bulkMoveForm.UpdateKeyValue();

                // Load new SBA and Division data
                await vm.GetSbaIdListByUtilityId();
                await vm.LoadDivisionList();
            } else {
                // Utility cleared, clear all dependent fields
                vm.clearDependentFields();
            }
        },
        GetSbaIdListByUtilityId: function () {
            var vm = this;
            let selctedValue = [];
            vm.SbaOptions = [];
            var url = `UtilityId=${vm.UtilityId}`;
            locateBillingService.GetSbaIdListByUtilityId(url).then(response => {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        json.forEach(item => {
                            let obj = {
                                name: item.NAME,
                                value: item.SBA_ID
                            };
                            selctedValue.push(obj);
                        });
                    }
                }
                // Clear SBA value and set new options, also clear dependent fields
                vm.$refs.bulkMoveForm.updateProperty("SbaId", "value", "");
                vm.$refs.bulkMoveForm.updateProperty("SbaId", "config.options", selctedValue);
                vm.SbaOptions = selctedValue;

                // Clear DivisionId and InvoiceNumber since they depend on SBA
                vm.$refs.bulkMoveForm.updateProperty("DivisionId", "config.options", []);
                vm.$refs.bulkMoveForm.updateProperty("DivisionId", "value", "");
                vm.$refs.bulkMoveForm.updateProperty("InvoiceNumber", "config.options", []);
                vm.$refs.bulkMoveForm.updateProperty("InvoiceNumber", "value", "");

                vm.$refs.bulkMoveForm.UpdateKeyValue();
            }).catch(error => {
                console.error('Error loading SBA data:', error);
                // Clear SBA and dependent fields on error
                vm.$refs.bulkMoveForm.updateProperty("SbaId", "config.options", []);
                vm.$refs.bulkMoveForm.updateProperty("SbaId", "value", "");
                vm.$refs.bulkMoveForm.updateProperty("DivisionId", "config.options", []);
                vm.$refs.bulkMoveForm.updateProperty("DivisionId", "value", "");
                vm.$refs.bulkMoveForm.updateProperty("InvoiceNumber", "config.options", []);
                vm.$refs.bulkMoveForm.updateProperty("InvoiceNumber", "value", "");
                vm.$refs.bulkMoveForm.UpdateKeyValue();
            });
        },
        LoadDivisionList() {
            var vm = this;
            vm.isLoading = true;
            var url = `clientId=${vm.clientId}&utilityIds=${vm.UtilityId}`;
            locateBillingService.LoadClientDivisionListByUtilityIds(url).then(response => {
                vm.isLoading = false;
                let SelectedDivisionList = [];
                var DivisionList = response.data;
                if (DivisionList != null) {
                    DivisionList.forEach(item => {
                        let obj = {
                            name: item.NAME,
                            value: item.VALUE
                        };
                        SelectedDivisionList.push(obj);
                    });
                }
                // Clear division and invoice number values and options
                vm.$refs.bulkMoveForm.updateProperty("DivisionId", "value", "");
                vm.$refs.bulkMoveForm.updateProperty("DivisionId", "config.options", SelectedDivisionList);
                vm.$refs.bulkMoveForm.updateProperty("InvoiceNumber", "config.options", []);
                vm.$refs.bulkMoveForm.updateProperty("InvoiceNumber", "value", "");
                vm.$refs.bulkMoveForm.UpdateKeyValue();
            }).catch(error => {
                vm.isLoading = false;
                vm.$refs.bulkMoveForm.updateProperty("DivisionId", "config.options", []);
                vm.$refs.bulkMoveForm.updateProperty("DivisionId", "value", "");
                vm.$refs.bulkMoveForm.updateProperty("InvoiceNumber", "config.options", []);
                vm.$refs.bulkMoveForm.updateProperty("InvoiceNumber", "value", "");
                vm.$refs.bulkMoveForm.UpdateKeyValue();
                console.error('Error loading divisions:', error);
            });
        },
        onDivisionChange(value, x) {
            var vm = this;
            if (x && x.value) {
                vm.isLoading = true;
                var url = `clientId=${vm.clientId}&divisionId=${x.value}&type=division`;
                locateBillingService.LoadInvoiceNumberList(url).then(response => {
                    vm.isLoading = false;
                    let SelectedInvoiceNumberList = [];
                    var InvNumberList = response.data;
                    if (InvNumberList != null) {
                        InvNumberList.forEach(item => {
                            let obj = {
                                name: item.NAME,
                                value: item.BILL_ID
                            };
                            SelectedInvoiceNumberList.push(obj);
                        });
                    }
                    // Clear current invoice number value and set new options
                    vm.$refs.bulkMoveForm.updateProperty("InvoiceNumber", "value", "");
                    vm.$refs.bulkMoveForm.updateProperty("InvoiceNumber", "config.options", SelectedInvoiceNumberList);
                    vm.$refs.bulkMoveForm.UpdateKeyValue();
                }).catch(error => {
                    vm.isLoading = false;
                    vm.$refs.bulkMoveForm.updateProperty("InvoiceNumber", "config.options", []);
                    vm.$refs.bulkMoveForm.updateProperty("InvoiceNumber", "value", "");
                    vm.$refs.bulkMoveForm.UpdateKeyValue();
                    console.error('Error loading invoice numbers:', error);
                });
            } else {
                // Division cleared, clear invoice number
                vm.$refs.bulkMoveForm.updateProperty("InvoiceNumber", "config.options", []);
                vm.$refs.bulkMoveForm.updateProperty("InvoiceNumber", "value", "");
                vm.$refs.bulkMoveForm.UpdateKeyValue();
            }
        },
        clearDependentFields() {
            // Clear options
            this.$refs.bulkMoveForm.updateProperty("UtilityName", "config.options", []);
            this.$refs.bulkMoveForm.updateProperty("SbaId", "config.options", []);
            this.$refs.bulkMoveForm.updateProperty("DivisionId", "config.options", []);
            this.$refs.bulkMoveForm.updateProperty("InvoiceNumber", "config.options", []);

            // Clear values
            this.$refs.bulkMoveForm.updateProperty("UtilityName", "value", "");
            this.$refs.bulkMoveForm.updateProperty("SbaId", "value", "");
            this.$refs.bulkMoveForm.updateProperty("DivisionId", "value", "");
            this.$refs.bulkMoveForm.updateProperty("InvoiceNumber", "value", "");

            this.$refs.bulkMoveForm.UpdateKeyValue();
        },
        onSubmit(formData) {
            this.validateBulkMoveConditions(formData);
        },
        validateBulkMoveConditions(formData) {
            if (this.oldUtilityList === formData.UtilityName &&
                this.activityIdList &&
                formData.SbaId &&
                formData.InvoiceNumber) {
                this.isLoading = true;
                locateBillingService.ValidateBMUtilitySbaIdInvoiceNo({
                    ClientId: formData.ClientName,
                    UtilityName: formData.UtilityName,
                    BillId: formData.InvoiceNumber,
                    SbaId: this.getSbaText(formData.SbaId), 
                    BeSbaId: formData.SbaId, 
                    DivisionId: formData.DivisionId,
                    LineItemIds: this.lineItemIds,
                    TicketNumber: this.ticketNumberList,
                    ActivityId: this.activityIdList,
                    WorkDateFrom: this.workDateFrom,
                    WorkDateTo: this.workDateTo
                }).then(response => {
                    this.isLoading = false;
                    if (response.data.flag === "failure" &&
                        (response.data.StatusCode === "Invalid" ||
                         response.data.StatusCode === "Error" ||
                         response.data.StatusCode === "Exception")) {
                        this.showAlert("BulkMove cannot be used on the same Utility, Sba_Id, and Invoice#.", "warning");
                        return;
                    }
                    this.showBulkMoveConfirmation(formData);
                }).catch(error => {
                    this.isLoading = false;
                    this.showAlert(this.$t("ErrorWhileProcessingRequest"), "failure");
                    console.error('Validation error:', error);
                });
            } else {
                this.showBulkMoveConfirmation(formData);
            }
        },
        showBulkMoveConfirmation(formData) {
            this.selectedFormData = formData;
            this.confirmationMessage = this.$t('BulkMoveConfirmMoveMessage').replace('{0}', this.selectedClientNames.join(', '));
            this.showConfirmationDialog = true;
        },
        cancelConfirmation() {
            this.showConfirmationDialog = false;
            this.selectedFormData = null;
        },
        confirmBulkMove() {
            this.showConfirmationDialog = false;
            this.performBulkMove(this.selectedFormData);
            this.selectedFormData = null;
        },
        performBulkMove(formData) {
            this.isLoading = true;
            var vm =this;
            const sbaText = this.getSbaText(formData.SbaId);
            const postData = {
                UtilityName: formData.UtilityName,
                ClientId: formData.ClientName,
                SbaId: sbaText, 
                BeSbaId: formData.SbaId, 
                DivisionId: formData.DivisionId,
                LineItemIds: this.lineItemIds,
                ReqFrom: this.requestFrom,
                TicketNumber: this.ticketNumberList,
                ActivityId: this.activityIdList,
                OldClient: this.oldClientList,
                OldUtility: this.oldUtilityList,
                WorkDateFrom: this.workDateFrom,
                WorkDateTo: this.workDateTo,
                BillId: formData.InvoiceNumber,
                AllInvoice: this.selectAllData ? JSON.stringify(this.selectAllData) : '',
                IsSelectedChecked: true,
                BulkMoveBatchId: this.BulkMoveBatchId
            };
            // Make API call
            locateBillingService.UpdateContractAndUtility(postData)
                .then(response => {
                    this.isLoading = false;
                    if (response.data.flag == "success" && response.data.statusCode !== -11 && response.data.statusCode !== 11) {
                        if (response.data.statusCode !== 101) {
                            vm.ShowAlert(vm.$t(response.data.msg), "success", true, vm.$t("Alert"));
                        }

                        if (response.data.bulkMoveResult != null) {
                            if (response.data.statusCode === 101) {
                                this.showAlert(this.$t("NoBillingRuleFound"), "warning");
                            } else {
                                this.showBulkMoveResultDialog(response.data.bulkMoveResult);
                            }
                        }
                    } else if (response.data.msg === "alreadyExists" && response.data.statusCode === 0) {
                        this.showSameRecordsDialogWithData(response.data.bulkMoveResult);
                    } else if (response.data.msg === "success" && response.data.statusCode === -11) {
                        this.showAlert(this.$t("BulkMOveAlreadyInQueueMessage"), "failure");
                    } else {
                        this.showAlert(response.data.msg || this.$t("ErrorWhileProcessingRequest"), "failure");
                    }
                })
                .catch(error => {
                    this.isLoading = false;
                    this.showAlert(this.$t("ErrorWhileProcessingRequest"), "failure");
                    console.error('Bulk move error:', error);
                });
        },
        getSbaText(sbaId) {
            // Find SBA text from the options
            const sbaOptions = this.SbaOptions || [];
            const sbaOption = sbaOptions.find(option => option.value === sbaId);
            return sbaOption ? sbaOption.name : '';
        },
        showBulkMoveResultDialog(bulkMoveResult) {
            this.isLoading = false;
            this.showSuccessDialog = true;
            this.successMessage = `Total ${bulkMoveResult.totalRecords || 0} records are marked as unprocessed. Records are in queue to process and will be processed soon. Scheduler runs every 10 minutes to process these records.`;
        },
        showSameRecordsDialogWithData(bulkMoveResult) {
            this.sameRecordsMessage = "Following are the details that Client, Utility, SBA Id OR Invoice grouping same please update it and try again.";
            this.sameRecordsData = bulkMoveResult || [];
            this.showSameRecordsDialog = true;
        },
        handleBulkMoveComplete() {
            // Clear selections and close dialog
            this.$emit('bulk-move-complete');
        },
        setBulkMoveData(data) {
            // Update component data with bulk move parameters
            this.requestFrom = data.requestFrom || this.requestFrom;
            this.ticketNumberList = data.ticketNumberList || this.ticketNumberList;
            this.activityIdList = data.activityIdList || this.activityIdList;
            this.oldClientList = data.oldClientList || this.oldClientList;
            this.oldUtilityList = data.oldUtilityList || this.oldUtilityList;
            this.workDateFrom = data.workDateFrom || this.workDateFrom;
            this.workDateTo = data.workDateTo || this.workDateTo;
            this.lineItemIds = data.lineItemIds || this.lineItemIds;
            this.selectAllData = data.selectAllData || this.selectAllData;
        },
        showAlert(message, type) {
            // Use global alert system or emit event
            this.$emit('show-alert', { message, type });
        },
        cancelForm() {
            this.closeDialog();
        },
         Close: function () {
            this.$parent.IsChangeLineItem = false;
        },
        closeSuccessDialog() {
            this.showSuccessDialog = false;
            this.Close();
        },
        closeSameRecordsDialog() {
            this.showSameRecordsDialog = false;
        }
    }
};
</script>

<style scoped>
.my-popups .modal-dialog {
    max-width: 90%;
}

.bg-f5f5f5 {
    background-color: #f5f5f5;
}

.thead-bg {
    background-color: #f8f9fa;
}

.text-danger {
    color: #dc3545 !important;
}
</style>
