            DataType: 'nvarchar',
            SelectedValue: '',
            IsViewTicket: false,
            TicketContent: '',
            TransmissionId: '',
            DivisionId: '',
            currentAction:'CatchAll',
            UtilityId: '',
            ServiceId:'',
            LocateId:'',
            AfterHours: '',
            UtilityAbbrev: '',
            FlagReason: '',
        }
    },
    created: async function () {
        var vm = this;
        await vm.GetStatusCodes();
        await vm.GetTicketTypes();
        await vm.GetActivityIdDropdownListByTicketNumber();
        await vm.GetWorkCodeData();
        await vm.GetDivisionDropdownData();
        await vm.LoadAdditionalInfoByClientId();
        await vm.GetSbaIdListByUtilityId();
        await vm.GetCostCenters();
        await vm.fetchclientlist();
        await vm.GetInvoiceLineItem();
    },
    methods: {
        GetInvoiceLineItem: async function () {
            var vm = this;
             vm.isLoading = true;
            var url = `lineBatchId=&billID=0&lineItemId=${vm.lineItemId}&columnId=${vm.columnId}&ticketType=${vm.ticketType}&isTicketMannual=${vm.isTicketMannual}`;
            locateBillingService.GetInvoiceLineItem(url).then(response => {
                if (response.data) {
                    var item = response.data[0];
                    vm.TransmissionId= item.TRANSMISSION_ID;
                    vm.DivisionId= item.DIVISION_ID;
                    vm.UtilityId= item.CLIENT_ADDITIONAL_ID;
                    vm.$refs.dynamicform.updateProperty("TicketNumber", "value", item.TICKETNUMBER);
                    vm.$refs.dynamicform.updateProperty("ActivityId", "value", item.ACTIVITY_ID);
                    vm.$refs.dynamicform.updateProperty("Description", "value", item.DESCRIPTION);
                    vm.$refs.dynamicform.updateProperty("Status", "value", item.STATUS);
                    vm.$refs.dynamicform.updateProperty("ClientId", "value", item.CLIENT_ID);
                    vm.$refs.dynamicform.updateProperty("TicketId", "value", item.TICKET_ID);
                    vm.$refs.dynamicform.updateProperty("LocatorName", "value", item.LOCATOR_NAME);
                    vm.$refs.dynamicform.updateProperty("StreetName", "value", item.STREETNAME);
                    vm.$refs.dynamicform.updateProperty("FeedName", "value", item.FEEDNAME);
                    vm.$refs.dynamicform.updateProperty("TransmissionId", "value", item.TRANSMISSION_ID);
                    vm.$refs.dynamicform.updateProperty("TicketType", "value", item.TICKETTYPE);
                    vm.$refs.dynamicform.updateProperty("WorkArea", "value", item.WORK_AREA);
                    vm.$refs.dynamicform.updateProperty("CallDate", "value", vm.$options.filters.formatDateTime(item.CALL_DATE));
                    vm.$refs.dynamicform.updateProperty("TransmitDate", "value", vm.$options.filters.formatDateTime(item.TRANSMIT_DATE));
                    vm.$refs.dynamicform.updateProperty("LocatorStartDate", "value", vm.$options.filters.formatDateTime(item.WORK_DATE));
                    vm.$refs.dynamicform.updateProperty("ParseDate", "value", vm.$options.filters.formatDateTime(item.PARSE_DATE));
                    vm.$refs.dynamicform.updateProperty("County", "value", item.COUNTY);
                    vm.$refs.dynamicform.updateProperty("State", "value", item.STATE);
                    vm.$refs.dynamicform.updateProperty("CallerName", "value", item.CALLER_NAME);
                    vm.$refs.dynamicform.updateProperty("CallerPhone", "value", item.CALLER_PHONE);
                    vm.$refs.dynamicform.updateProperty("WorkDoneFor", "value", item.WORK_DONE_FOR);
                    vm.$refs.dynamicform.updateProperty("Excavator", "value", item.EXCAVATOR);
                    vm.$refs.dynamicform.updateProperty("TypeOfWork", "value", item.TYPEOFWORK);
                    vm.$refs.dynamicform.updateProperty("LocatorEndDate","value",item.CLOSED_DATE ? vm.$options.filters.formatDateTime(item.CLOSED_DATE) : null);
                    vm.$refs.dynamicform.updateProperty("InitialStatusDate", "value", vm.$options.filters.formatDateTime(item.INITIAL_STATUS_DATE));
                    vm.$refs.dynamicform.updateProperty("LegalDuedate","value",item.LEGAL_DUE_DATE ? vm.$options.filters.formatDateTime(item.LEGAL_DUE_DATE) : null);
                    //vm.$refs.dynamicform.updateProperty("LegalDuedate", "value", vm.$options.filters.formatDateTime(item.LEGAL_DUE_DATE));
                    vm.$refs.dynamicform.updateProperty("TicketStartDate", "value", vm.$options.filters.formatDateTime(item.OUR_DUE_DATE));
                    vm.$refs.dynamicform.updateProperty("UtilityName", "value", item.UTILITY_NAME);
                    vm.$refs.dynamicform.updateProperty("FeetLocated", "value", item.FEET_LOCATED);
                    vm.$refs.dynamicform.updateProperty("MinutesWorked", "value", item.MINUTES_WORKED);
                    vm.$refs.dynamicform.updateProperty("Emergency", "value", item.EMERGENCY);
                    vm.$refs.dynamicform.updateProperty("Comment", "value", item.REMARKS); 
                    vm.$refs.dynamicform.updateProperty("UtilityId", "value", item.CLIENT_ADDITIONAL_ID);
                    vm.$refs.dynamicform.updateProperty("SbaId", "value", item.BE_SBA_ID);
                    vm.$refs.dynamicform.updateProperty("DistrictCode", "value", item.DISTRICT_CODE);
                    vm.$refs.dynamicform.updateProperty("WorkCodeId", "value", item.WORK_CODE_ID);
                    vm.$refs.dynamicform.updateProperty("DivisionId", "value", item.DIVISION_ID);
                    vm.$refs.dynamicform.updateProperty("UnitRate", "value", item.UNIT_RATE);
                    vm.$refs.dynamicform.updateProperty("UnitQuantity", "value", item.UNIT_WORKED); // or UNITS?
                    vm.$refs.dynamicform.updateProperty("UnitAmountTotal", "value", item.UNIT_TOTAL_AMOUNT);
                    vm.$refs.dynamicform.updateProperty("TermCode", "value", item.TERM_CODE);
                    vm.$refs.dynamicform.updateProperty("AdderNameLineItem", "value", item.ADDER_NAME);
                    vm.$refs.dynamicform.updateProperty("AdderUnitCost", "value", item.ADDER_UNIT_COST);
                    vm.$refs.dynamicform.updateProperty("AdderUnit", "value", item.ADDER_UNIT);
                    vm.$refs.dynamicform.updateProperty("NetTotal", "value", item.AMOUNT ?? item.UNIT_TOTAL_AMOUNT);

                    vm.ServiceId= item.SERVICECODE;
                    vm.LocateId=item.LOCATE_ID;
                    vm.AfterHours= item.AFTER_HOURS;
                    vm.UtilityAbbrev= item.UTILITY_ABBREV;
                    vm.FlagReason= item.FLAG_REASON;
                    // Set old values for change tracking
                    vm.oldValues = {
                        TicketNumber: item.TICKETNUMBER,
                        ActivityId: item.ACTIVITY_ID,
                        Description: item.DESCRIPTION,
                        Status: item.STATUS,
                        ClientId: item.CLIENT_ID,
                        TicketId: item.TICKET_ID,
                        LocatorName: item.LOCATOR_NAME,
                        StreetName: item.STREETNAME,
                        FeedName: item.FEEDNAME,
                        TransmissionId: item.TRANSMISSION_ID,
                        TicketType: item.TICKETTYPE,
                        WorkArea: item.WORK_AREA,
                        CallDate: vm.$options.filters.formatDateTime(item.CALL_DATE),
                        TransmitDate: vm.$options.filters.formatDateTime(item.TRANSMIT_DATE),
                        LocatorStartDate: vm.$options.filters.formatDateTime(item.WORK_DATE),
                        ParseDate: vm.$options.filters.formatDateTime(item.PARSE_DATE),
                        County: item.COUNTY,
                        State: item.STATE,
                        CallerName: item.CALLER_NAME,
                        CallerPhone: item.CALLER_PHONE,
                        WorkDoneFor: item.WORK_DONE_FOR,
                        Excavator: item.EXCAVATOR,
                        TypeOfWork: item.TYPEOFWORK,
                        LocatorEndDate: vm.$options.filters.formatDateTime(item.CLOSED_DATE),
                        InitialStatusDate: vm.$options.filters.formatDateTime(item.INITIAL_STATUS_DATE),
                        LegalDuedate: vm.$options.filters.formatDateTime(item.LEGAL_DUE_DATE),
                        TicketStartDate: vm.$options.filters.formatDateTime(item.OUR_DUE_DATE),
                        UtilityName: item.UTILITY_NAME,
                        FeetLocated: item.FEET_LOCATED,
                        MinutesWorked: item.MINUTES_WORKED,
                        Emergency: item.EMERGENCY,
                        TicketImage: item.TICKETIMAGE,
                        Comment: item.REMARKS,
                        UtilityId: item.CLIENT_ADDITIONAL_ID,
                        SbaId: item.SBA_ID,
                        DistrictCode: item.DISTRICT_CODE,
                        WorkCodeId: item.WORK_CODE_ID,
                        DivisionId: item.DIVISION_ID,
                        UnitRate: item.UNIT_RATE,
                        UnitQuantity: item.UNIT_WORKED,
                        NetTotalManual: "0.00",
                        UnitAmountTotal: item.UNIT_TOTAL_AMOUNT,
                        TermCode: item.TERM_CODE,
                        AdderNameLineItem: item.ADDER_NAME,
                        AdderUnitCost: item.ADDER_UNIT_COST,
                        AdderUnit: item.ADDER_UNIT,
                        NetTotal: item.AMOUNT ?? item.UNIT_TOTAL_AMOUNT,
                        customlineitem: vm.IsTicketMannual,
                        ServiceId: item.SERVICECODE,
                        LocateId: item.LOCATE_ID,
                        AfterHours: item.AFTER_HOURS,
                        UtilityAbbrev: item.UTILITY_ABBREV,
                        FlagReason: item.FLAG_REASON
                    };
                    vm.onCustomLineItemChange(vm.IsTicketMannual);
                }
                  vm.isLoading = false;
            }).catch(() => {
                vm.isLoading = false;
            });
        }, 
        onSelectionUtilityChange(value,x) {
            var vm = this;
            vm.UtilityId = x.value;
            vm.GetSbaIdListByUtilityId();
        },
         fetchclientlist: function () {
            var vm = this;
            vm.isLoading = true;
            var url = `reqFrom=CUSTOM`;
            locateBillingService.getClientList(url).then(response => {
                vm.isLoading = false;
                vm.clientList = response.data.items;
                vm.$refs.dynamicform.updateProperty("ClientId", "config.options", vm.clientList);
                vm.$refs.dynamicform.UpdateKeyValue();
            }).catch(error => {
                vm.isLoading = false;
                vm.clientList = []; 
                vm.$refs.dynamicform.updateProperty("ClientId", "config.options", vm.clientList);
                vm.$refs.dynamicform.UpdateKeyValue();
                console.error('Error fetching client list:', error);
            }).finally(() => {
                vm.isLoading = false;
            });
        },       
         GetStatusCodes: async function () {
            var vm = this;
            let selctedValue = [];
            locateBillingService.GetStatusCodes().then(response => {
                if (response.data) {
                    var json = response.data;
