        },
        onCustomLineItemChange: function (value) {
            // Show/hide fields based on ticket/manual
            var vm = this;
            if (value === 'M' || vm.IsTicketFlatFee === 'F') {
                // Show manual fields
                vm.$refs.dynamicform.updateProperty("UnitRate", "visibility", true);
                vm.$refs.dynamicform.updateProperty("UnitQuantity", "visibility", true);
                if (vm.columnId == 0 || vm.columnId == null) {
                    vm.$refs.dynamicform.updateProperty("NetTotalManual", "visibility", true);
                }
                // Hide ticket fields
                vm.setTicketFieldsVisibility(false);
                // Disable adder fields
                vm.$refs.dynamicform.updateProperty("AdderNameLineItem", "disabled", true);
                vm.$refs.dynamicform.updateProperty("AdderUnitCost", "disabled", true);

                if (vm.IsTicketFlatFee === 'F') {
                    // For flat fee, hide UnitQuantity
                    vm.$refs.dynamicform.updateProperty("UnitQuantity", "visibility", false);
                    vm.$refs.dynamicform.updateProperty("WorkCodeId", "disabled", true);
                    vm.$refs.dynamicform.updateProperty("WorkCodeId", "value", "FF");
                }
            } else {
                // Show ticket fields, hide manual fields
                vm.setTicketFieldsVisibility(true);
                vm.$refs.dynamicform.updateProperty("UnitRate", "visibility", false);
                vm.$refs.dynamicform.updateProperty("UnitQuantity", "visibility", false);
                vm.$refs.dynamicform.updateProperty("NetTotalManual", "visibility", false);
                // Enable adder fields if AdderUnit > 0
                if (vm.AdderUnit > 0) {
                    vm.$refs.dynamicform.updateProperty("AdderNameLineItem", "disabled", false);
                    vm.$refs.dynamicform.updateProperty("AdderUnitCost", "disabled", false);
                }
            }
        },
        setTicketFieldsVisibility: function (visible) {
            var ticketFields = [
                "TicketNumber", "ActivityId", "Description", "Status", "ClientId", "TicketId",
                "LocatorName", "StreetName", "FeedName", "TransmissionId", "TicketType", "WorkArea",
                "CallDate", "TransmitDate", "LocatorStartDate", "ParseDate", "County", "State", "CallerName",
                "CallerPhone", "WorkDoneFor", "Excavator", "TypeOfWork", "LocatorEndDate", "InitialStatusDate",
                "LegalDuedate", "TicketStartDate", "UtilityName", "FeetLocated", "MinutesWorked", "Emergency",
                "TicketImage", "Comment", "UtilityId", "SbaId"
            ];
            ticketFields.forEach(field => {
                this.$refs.dynamicform.updateProperty(field, "visibility", visible);
            });
        },
        onActivityIdChange: function (value) {
            var vm = this;
            vm.ActivityId = value;
            if (vm.TicketNumber && value) {
                var url = `TicketNumber=${vm.TicketNumber}&ActivityId=${value}&ClientId=${vm.ClientId}`;
                locateBillingService.GetInvoiceLineItem(url).then(response => {
                    if (response.data) {
                        // Reload the form with new data
                        vm.GetInvoiceLineItem();
                    }
                });
            }
        },
         onSelectionChange(value,x) {
            // Ensure value is an array for multiple mode
            var vm = this;
            vm.isLoading = true;
            var url = `clientId=${x}&configId=&reqFrom=&clientIds=`;
            locateBillingService.getUtilityList(url).then(response => {
                vm.isLoading = false;
                  let UtilityList=[];
                  if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        json.forEach(item => {
                            let obj = {
                                name: item.UTILITY_NAME,
                                value: item.UTILITY_ID
                            };
                            UtilityList.push(obj);
                        });
                        vm.$refs.dynamicform.updateProperty("UtilityId", "config.options", UtilityList);
                        vm.$refs.dynamicform.UpdateKeyValue();
                        vm.UtilityId = vm.$refs.dynamicform.getPropertyValue("UtilityId", "value");
                        vm.GetSbaIdListByUtilityId();
                    }
                }
            }).catch(error => {
                vm.isLoading = false;
                vm.UtilityList = []; 
                vm.$refs.dynamicform.updateProperty("UtilityId", "config.options", UtilityList);
                vm.$refs.dynamicform.UpdateKeyValue();;
            }).finally(() => {
                vm.isLoading = false;
            });
        },
        onWorkCodeChange: function (x,y,z) {
            var vm = this;
            var url = `clientId=${vm.ClientId}&workCodeId=${y.value}&divisionId=${vm.DivisionId}`;
            locateBillingService.LoadRateByWorkCode(url).then(response => {
                if (response.data) {
                    var rate = parseFloat(response.data[0].WORK_RATE).toFixed(2);
                    if (vm.IsTicketMannual === 'M') {
                        vm.$refs.dynamicform.updateProperty("UnitRate", "value", rate);
                        vm.calculateNetTotalManual();
                    } else {
                        vm.$refs.dynamicform.updateProperty("UnitAmountTotal", "value", rate);
                        vm.calculateNetTotal();
                    }
                }
            });
        },
        onAdderNameChange: function (value) {
            var vm = this;
            var url = `clientId=${vm.ClientId}&workCodeId=${value}&divisionId=${vm.DivisionId}`;
            locateBillingService.LoadRateByWorkCode(url).then(response => {
                if (response.data) {
                    var rate = parseFloat(response.data).toFixed(2);
                    vm.$refs.dynamicform.updateProperty("AdderUnitCost", "value", rate);
                    vm.calculateNetTotal();
                }
            });
        },
        calculateNetTotal: function () {
            var vm = this;
            var unitAmountTotal = parseFloat(vm.$refs.dynamicform.getPropertyValue("UnitAmountTotal", "value") || 0);
            var adderUnitCost = parseFloat(vm.$refs.dynamicform.getPropertyValue("AdderUnitCost", "value") || 0);
            var adderUnit = parseFloat(vm.AdderUnit || 0);
            var total = unitAmountTotal + (adderUnitCost * adderUnit);
            vm.$refs.dynamicform.updateProperty("NetTotal", "value", total.toFixed(2));
        },
        calculateNetTotalManual: function () {
            var vm = this;
            var unitRate = parseFloat(vm.$refs.dynamicform.getPropertyValue("UnitRate", "value") || 0);
            var unitQuantity = parseFloat(vm.$refs.dynamicform.getPropertyValue("UnitQuantity", "value") || 0);
            if (unitRate === 0) unitRate = 0;
            if (unitQuantity === 0) unitQuantity = 0;
            var total = unitQuantity * unitRate;
            var total1 = parseFloat(Math.round(total * 100) / 100).toFixed(2);
            if (!isNaN(total1)) {
                vm.$refs.dynamicform.updateProperty("NetTotalManual", "value", total1);
            }
        },
        onSubmit(formfields, val) {
            var vm = this;
            vm.isLoading = true;
            vm.UpdateChangedValues(formfields);
            var ProcessLogJson = JSON.stringify(vm.MakeJsonForProcessLog(formfields));
            let text = val.replace(/<[^>]*>/g, '').trim();
            let isSaveClicked = text === 'Save And Close' ? 1 : 0;
            var postData = {
                columnId: vm.columnId,
                SetupId: "",
                BatchId: "",
                BillId: 0,
                IsAdd: vm.IsAdd,
                IsReadOnlyForReport: "",
                IsTicketMannual: vm.isTicketMannual,
                IsTicketFlatFee:vm.ticketType,
                LineItemId: vm.lineItemId,
                DateOfInvoiceLine: "",
                InvoiceBatchId: "",
                RecordOnSearchTicketNumer: "yes",
                TicketNumber: formfields.TicketNumber,
                ActivityId: formfields.ActivityId,
                Description: formfields.Description,
                Status: formfields.Status,
                ClientId: formfields.ClientId,
                TicketId: formfields.TicketId,
                LocatorName: formfields.LocatorName,
                StreetName: formfields.StreetName,
                FeedName: formfields.FeedName,
                TransmissionId: formfields.TransmissionId,
                TicketType: formfields.TicketType,
                WorkArea: formfields.WorkArea,
                CallDate: formfields.CallDate,
                TransmitDate: formfields.TransmitDate,
                WorkDate: formfields.LocatorStartDate,
                ParseDate: formfields.ParseDate,
                County: formfields.County,
                State: formfields.State,
                CallerName: formfields.CallerName,
                CallerPhone: formfields.CallerPhone,
                WorkDoneFor: formfields.WorkDoneFor,
                Excavator: formfields.Excavator,
                TypeOfWork: formfields.TypeOfWork,
                ClosedDate: formfields.LocatorEndDate,
                InitialStatusDate: formfields.InitialStatusDate,
                LegalDuedate: formfields.LegalDuedate,
                OurDueDate: formfields.TicketStartDate,
                UtilityName: formfields.UtilityName,
                FeetLocated: formfields.FeetLocated,
                MinutesWorked: formfields.MinutesWorked,
                Emergency: formfields.Emergency,
                Comment: formfields.Comment,
                ClientAdditionalId: formfields.UtilityId,
                SbaId: formfields.SbaId,
                DistrictCode: formfields.DistrictCode,
                WorkCodeId: formfields.WorkCodeId,
                DivisionId: formfields.DivisionId,
                UnitRate: formfields.UnitRate,
                UnitQuantity: formfields.UnitQuantity,
                UnitAmountTotal: formfields.UnitAmountTotal,
                TermCode: formfields.TermCode,
                AdderNameLineItem: formfields.AdderNameLineItem,
                AdderUnitCost: formfields.AdderUnitCost,
                AdderUnit: formfields.AdderUnit,
                NetTotal: formfields.NetTotal,
                ServiceId: vm.ServiceId,
                LocateId: vm.LocateId,
                AfterHours: vm.AfterHours,
                UtilityAbbrev: vm.UtilityAbbrev,
                FlagReason: vm.FlagReason,
                ProcessLogJson: ProcessLogJson,
                newStringifyModel: JSON.stringify(formfields),
                oldStringifyModel: JSON.stringify(vm.oldValues),
                isSaveClicked: isSaveClicked
            };

            locateBillingService.UpdateInvoiceLineItem(postData).then(response => {
                vm.isLoading = false;
                if (response.data && response.data.msg === "Success") {
                    vm.ShowAlert(vm.$t('RecordUpdatedSuccessfully'), "success", true, vm.$t("Alert"));
                    if (isSaveClicked == 1|| isSaveClicked == 0) {
                        vm.$parent.FetchBilledUnbilledRecords();
                        vm.Close();
                    } 
                    else if (isSaveClicked === 0) {
                        //vm.resetForm();
                    }
                } else if (response.data && response.data.msgFor === "invalid") {
                    if (response.data.invalidKey) {
                        response.data.invalidKey.forEach(function (ctr) {
                            vm.$refs.dynamicform.showError(ctr.ctrId, ctr.ctrMsg);
                        });
                    }
                    vm.ShowAlert(response.data.msg, "failure", true, vm.$t("Alert"));
                } else {
                    vm.ShowAlert(response.data.msg, response.data.msgFor, true, vm.$t("Alert"));
                }
            }).catch(() => {
                vm.isLoading = false;
                vm.ShowAlert(vm.$t("ErrorWhileProcessingRequest"), "failure", true, vm.$t("Alert"));
            });
        },
        UpdateChangedValues(formfields) {
            var data = "{";
            for (var key in formfields) {
                if (formfields[key] !== this.oldValues[key]) {
                    data += '"' + key + '":' + JSON.stringify(formfields[key]) + ",";
                }
            }
            if (data === "{") {
                this.$refs.dynamicform.updateProperty("Comment", "value", "");
            } else {
                data = data.substr(0, data.length - 1) + "}";
                this.$refs.dynamicform.updateProperty("Comment", "value", data);
            }
        },
        MakeJsonForProcessLog(formfields) {
            var dataArray = [];
            for (var key in formfields) {
                if (formfields[key] !== this.oldValues[key] && key !== "Comment") {
                    dataArray.push({
                        ControlName: key,
                        OldData: this.oldValues[key] || "",
                        NewData: formfields[key] || ""
                    });
                }
            }
            return dataArray;
        },
        // resetForm: function () {
        //     // Reset form for save & new
        //     this.$refs.dynamicform.reset();
        //     this.oldValues = {};
        // },
        viewTicketImage: function () {
            var vm = this;
            vm.IsViewTicket = true;
            var url = `TicketNumber=${vm.TicketNumber}&TransmissionId=${vm.TransmissionId}`;
            locateBillingService.ViewTicketImage(url).then(response => {
                if (response.data && response.data.length > 0) {
                    vm.TicketContent = response.data[0].RAW_IMAGE;
                }
            });
        },
        CloseTemplate: function () {
            this.IsViewTicket = false;
        },
        Close: function () {
            this.$parent.IsEditInvoiceItem = false;
        },
    }
}
</script>
