<template>
<section>
    <loader :is-visible="isLoading"></loader>
    <tg-list ref="tgList" :listType="['List']" :IsShowAction="true" :showCheckBox="false" ModuleName="BillingValidationData" SubModuleCode="TicketLog" IdentityColumn="COLUMN_ID" :HeaderText="$t('TicketDetails')" :ListData="ListingData" :HeaderData="Headers" :widgets="widgets" :HeaderButtons="Buttons" :TotalRecords="TotalRecords" :RenderRowActionMethod="renderActions" :ListDataCallBackFunction="FetchTicketLog" :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')" v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata" :LegendArray="LegendArray" v-on:LeftsearchButtonClick="leftCommonSearch" v-on:ActionButtonClick="actionButtonClick" :SearchFields="leftSearchFields" SearchPosition="top">
        <template v-slot:slotdata="{ data }">
            <template v-if="data.column.COLUMN_NAME == 'TICKETNUMBER'">
                <div class="single-action">
                    <a href="javascript:;" @click="toggleExpandRow(data.row, $event)" :class="{ 'parentwithchild': true, 'expandchild': expandedRows[data.row.COLUMN_ID] }" :data-id="`_${data.row.COLUMN_ID}`" :data-ticketnumber="data.row.TICKETNUMBER" :data-activityid="data.row.ACTIVITY_ID">
                        <em :class="expandedRows[data.row.COLUMN_ID] ? 'fa fa-minus-circle' : 'fa fa-plus-circle rotate-icon'"></em>
                    </a>
                    {{ data.row.TICKETNUMBER }}
                </div>
            </template>
            <template v-if="data.column.COLUMN_NAME == 'WORK_DATE'">
                <span v-if="data.row.WORK_DATE" :title="data.row.WORK_DATE">
                    {{ formatDate(data.row.WORK_DATE) }}
                </span>
                <span v-else>--</span>
            </template>
            <template v-if="data.column.COLUMN_NAME == 'PROCESS_DATE'">
                <span v-if="data.row.PROCESS_DATE" :title="data.row.PROCESS_DATE">
                    {{ formatDate(data.row.PROCESS_DATE) }}
                </span>
                <span v-else>--</span>
            </template>
        </template>
    </tg-list>
    <!-- Ticket Image Modal -->
    <transition name="modal">
        <div class="my-popups">
            <loader :is-visible="isLoading"></loader>
            <div class="modal d-block dialog" v-if="IsViewTicket">
                <div class="modal-dialog modal-lg">
                    <div class="modal-content">
                        <div class="modal-header">
                            <h4 class="modal-title">{{ $t('TicketImage') }}</h4>
                            <button type="button" class="close" v-on:click="CloseTemplate">
                                <em class="fa fa-times" aria-hidden="true"></em>
                            </button>
                        </div>
                        <div class="modal-body">
                            <div class="col-md-12">
                                <div class="listing" v-if="TicketContent">
                                    <div v-html="TicketContent" style="border: 1px solid #ddd; padding: 10px; margin-top: 10px; min-height: 50vh; overflow-y: auto;">
                                    </div>
                                </div>
                                <div v-else class="listing text-danger text-center border-0">
                                    {{ $t("NorecordfoundText") }}
                                </div>
                            </div>
                        </div>
                        <div class="modal-footer">
                            <button @click="CloseTemplate" class="btn btn-danger">
                                <em class="fa fa-times pr-2" aria-hidden="true"></em> {{ $t('Cancel') }}
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </transition>
    <transition name="modal">
        <section>
            <div class="modal my-popups right fade show d-block" v-if="IsChildData">
                <div class="modal-dialog modal-dialog-right">
                    <div class="modal-content">
                        <div class="modal-header mheader-user">
                            <button type="button" class="close" v-on:click="CloseChildRow">
                                <em class="fa fa-times" aria-hidden="true"></em>
                            </button>
                            <h4 class="modal-title">{{ $t('ProcessLog') }}</h4>
                        </div>
                        <div class="modal-body p-0">
                            <div class="tab-content" style="overflow-x: hidden; height: 800px;  max-height: 800px;">
                                <div class="col-md-12">
                                    <div class="listing" v-if="TicketActivityContent && TicketActivityContent.length > 0">
                                        <div class="tab-style">
                                            <div class="tab_container">
                                                <div class="responsive-tabs-container accordion-xs accordion-sm">
                                                    <ul class="nav nav-tabs all-tabs responsive-tabs cust mt-3">
                                                        <li class="nav-item">
                                                            <a class="nav-link" :class="{ active: activeTab === 'OldProcessLog' }" @click="activeTab = 'OldProcessLog'">{{ $t('OldProcessLog') }}</a>
                                                        </li>
                                                        <li class="nav-item">
                                                            <a class="nav-link" :class="{ active: activeTab === 'NewProcessLog' }" @click="activeTab = 'NewProcessLog'">{{ $t('Process') }} {{ $t('Log') }}</a>
                                                        </li>
                                                    </ul>
                                                    <div class="tab-content">
                                                        <div :class="{ active: activeTab === 'OldProcessLog' }" class="tab-pane">
                                                            <div class="container-fluid p-0">
                                                                <div class="listing mt-2">
                                                                    <div class="table-responsive" id="divflagrecordlisting">
                                                                        <table id="tblprocesseLog" class="table table-bordered dt-responsive nowrap mt-0 w-100">
                                                                            <thead class="thead-bg">
                                                                                <tr>
                                                                                    <th style="width:160px;" class="text-center">
                                                                                        <span>{{ $t('SerialNumber') }}</span>
                                                                                    </th>
                                                                                    <th style="width:700px;">
                                                                                        <span>{{ $t('ServiceCondition') }}</span>
                                                                                    </th>
                                                                                    <th>
                                                                                        <span>{{ $t('IsApplied') }}</span>
                                                                                    </th>
                                                                                </tr>
                                                                            </thead>
                                                                            <tbody>
                                                                                <tr v-if="!TicketActivityContent || TicketActivityContent.filter(x => x.TYPE === 'PROCESSLOG').length === 0">
                                                                                    <td colspan="3" class="text-center text-danger no-record">
                                                                                        {{ $t('NoRecordfound') }}
                                                                                    </td>
                                                                                </tr>
                                                                                <template v-else>
                                                                                    <tr v-for="(item, index) in TicketActivityContent.filter(x => x.TYPE === 'PROCESSLOG')" :key="index">
                                                                                        <td class="text-center">{{ index + 1 }}.</td>
                                                                                        <td>{{ item.SERVICE_CONDITION }}</td>
                                                                                        <td>{{ item.ISAPPLICABLE }}</td>
                                                                                    </tr>
                                                                                    <tr v-if="processLogData">
                                                                                        <td colspan="3" class="single-action">
                                                                                            <a href="javascript:;" class="ancviewbillingflowchart" @click="viewBillingFlowchart(processLogData)">
                                                                                                <em class="fa fa-eye"></em> {{ $t('ClickheretoviewBillingRule') }}
                                                                                            </a>
                                                                                        </td>
                                                                                    </tr>
                                                                                </template>
                                                                            </tbody>
                                                                        </table>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                        <div :class="{ active: activeTab === 'NewProcessLog' }" class="tab-pane">
                                                            <div class="container-fluid p-0" id="divNewProcessLogData">
                                                                <div class="listing mt-2">
                                                                    <div class="table-responsive" id="divflagrecordlisting">
                                                                        <table id="tblprocesseLog" class="table table-bordered dt-responsive nowrap mt-0 w-100">
                                                                            <thead class="thead-bg">
                                                                                <tr>
                                                                                    <th width="10%" class="text-center">
                                                                                        <span>{{ $t('SerialNumber') }}</span>
                                                                                    </th>
                                                                                    <th width="10%">
                                                                                        <span>{{ $t('Date') }}</span>
                                                                                    </th>
                                                                                    <th width="10%" class="text-center">
                                                                                        <span>{{ $t('BillingRule') }}</span>
                                                                                    </th>
                                                                                    <th width="70%">
                                                                                        <span>{{ $t('ServiceCondition') }}</span>
                                                                                    </th>
                                                                                </tr>
                                                                            </thead>
                                                                            <tbody>
                                                                                <tr v-if="!newProcessLogData || newProcessLogData.length === 0">
                                                                                    <td colspan="4" class="text-center text-danger no-record">
                                                                                        {{ $t('NoRecordfound') }}
                                                                                    </td>
                                                                                </tr>
                                                                                <template v-else>
                                                                                    <tr v-for="(item, index) in newProcessLogData" :key="index">
                                                                                        <td style="vertical-align:top;" class="text-center">{{ index + 1 }}.</td>
                                                                                        <td style="vertical-align:top;">{{ item.DATEOFWORK ? new Date(item.DATEOFWORK).toLocaleDateString() : '' }}</td>
                                                                                        <td style="vertical-align:top;" class="text-center single-action">
                                                                                            <a v-if="item.CONFIGURATION_ID > 0" href="javascript:;" :title="$t('ClickheretoviewBillingRule')" class="ancviewbillingflowchart enable viewicon" @click="viewBillingFlowchart(item)">
                                                                                                <em class="fa fa-eye text-primary"></em>
                                                                                            </a>
                                                                                            <a v-else href="javascript:;" class="disable viewicon">
                                                                                                <em class="fa fa-eye disabled text-secondary"></em>
                                                                                            </a>
                                                                                        </td>
                                                                                        <td style="vertical-align:top;white-space:pre-wrap;">{{ item.SERVICE_CONDITION }}</td>
                                                                                    </tr>
                                                                                </template>
                                                                            </tbody>
                                                                        </table>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="listing mt-3">
                                            <div class="heading-border">
                                                <h5 class="h5"><span>{{ $t('Flag') }}</span></h5>
                                            </div>
                                            <div class="table-responsive mt-2" id="divflagrecordlisting">
                                                <table id="tblflagList" class="table table-bordered dt-responsive nowrap mt-0 w-100">
                                                    <thead class="thead-bg">
                                                        <tr>
                                                            <th><span>{{ $t('ActivityId') }}</span></th>
                                                            <th><span>{{ $t('FlagReason') }}</span></th>
                                                            <th><span>{{ $t('Data') }}</span></th>
                                                            <th><span>{{ $t('Status') }}</span></th>
                                                            <th><span>{{ $t('ModifiedBy') }}</span></th>
                                                            <th><span>{{ $t('ModifiedDate') }}</span></th>
                                                        </tr>
                                                    </thead>
                                                    <tbody>
                                                        <tr v-if="!TicketActivityContent || TicketActivityContent.filter(x => x.TYPE === 'FLAG').length === 0">
                                                            <td colspan="6" class="text-center text-danger no-record">
                                                                {{ $t('NoRecordfound') }}
                                                            </td>
                                                        </tr>
                                                        <tr v-else v-for="item in TicketActivityContent.filter(x => x.TYPE === 'FLAG')" :key="item.ACTIVITY_ID + item.FLAGREASSON">
                                                            <td>{{ item.ACTIVITY_ID }}</td>
                                                            <td>{{ item.FLAGREASSON }}</td>
                                                            <td :title="item.CHANGEDDATA">
                                                                <span>{{ item.CHANGEDDATA }}</span>
                                                            </td>
                                                            <td>{{ item.STATUS }}</td>
                                                            <td>{{ item.MODIFIEDBY }}</td>
                                                            <td>{{ item.MODIFIEDAT }}</td>
                                                        </tr>
                                                    </tbody>
                                                </table>
                                            </div>
                                        </div>
                                        <div class="listing mt-3">
                                            <div class="heading-border">
                                                <h5 class="h5"><span>{{ $t('Processed') }}</span></h5>
                                            </div>
                                            <div class="table-responsive mt-2" id="divflagrecordlisting">
                                                <table id="tblprocessedList" class="table table-bordered dt-responsive nowrap mt-0 w-100">
                                                    <thead class="thead-bg">
                                                        <tr>
                                                            <th><span>{{ $t('ActivityId') }}</span></th>
                                                            <th><span>{{ $t('TicketType') }}</span></th>
                                                            <th><span>{{ $t('WorkDoneFor') }}</span></th>
                                                            <th><span>{{ $t('WorkType') }}</span></th>
                                                            <th><span>{{ $t('TransmitDate') }}</span></th>
                                                            <th><span>{{ $t('WorkDate') }}</span></th>
                                                            <th><span>{{ $t('WorkCode') }}</span></th>
                                                            <th><span>{{ $t('Cost') }}</span></th>
                                                            <th><span>{{ $t('State') }}</span></th>
                                                            <th><span>{{ $t('ServiceCondition') }}</span></th>
                                                        </tr>
                                                    </thead>
                                                    <tbody>
                                                        <tr v-if="!TicketActivityContent || TicketActivityContent.filter(x => x.TYPE === 'PROCESSED').length === 0">
                                                            <td colspan="10" class="text-center text-danger no-record">
                                                                {{ $t('NoRecordfound') }}
                                                            </td>
                                                        </tr>
                                                        <tr v-else v-for="item in TicketActivityContent.filter(x => x.TYPE === 'PROCESSED')" :key="item.ACTIVITY_ID + item.TICKET_TYPE">
                                                            <td>{{ item.ACTIVITY_ID }}</td>
                                                            <td>{{ item.TICKET_TYPE }}</td>
                                                            <td>{{ item.WORKFOR }}</td>
                                                            <td>{{ item.WORKTYPE }}</td>
                                                            <td>{{ item.TRANSMIT_DATE }}</td>
                                                            <td>{{ item.DATEOFWORK }}</td>
                                                            <td>{{ item.WORKCODE_ID }}</td>
                                                            <td>{{ item.COST ? item.COST.toFixed(2) : '0.00' }}</td>
                                                            <td>{{ item.STATE }}</td>
                                                            <td style="white-space:normal;overflow:visible">{{ item.SERVICE_CONDITION }}</td>
                                                        </tr>
                                                    </tbody>
                                                </table>
                                            </div>
                                        </div>
                                        <div class="listing mt-3">
                                            <div class="heading-border">
                                                <h5 class="h5"><span>{{ $t('Banking') }}</span></h5>
                                            </div>
                                            <div class="table-responsive mt-2" id="divflagrecordlisting">
                                                <table id="tblbankList" class="table table-bordered dt-responsive nowrap mt-0 w-100">
                                                    <thead class="thead-bg">
                                                        <tr>
                                                            <th><span>{{ $t('ActivityId') }}</span></th>
                                                            <th><span>{{ $t('Status') }}</span></th>
                                                            <th><span>{{ $t('Units') }}</span></th>
                                                            <th><span>{{ $t('Category') }}</span></th>
                                                            <th class="text-center"><span>{{ $t('Date') }}</span></th>
                                                        </tr>
                                                    </thead>
                                                    <tbody>
                                                        <tr v-if="!TicketActivityContent || TicketActivityContent.filter(x => x.TYPE === 'BANK').length === 0">
                                                            <td colspan="5" class="text-center text-danger no-record">
                                                                {{ $t('NoRecordfound') }}
                                                            </td>
                                                        </tr>
                                                        <tr v-else v-for="item in TicketActivityContent.filter(x => x.TYPE === 'BANK')" :key="item.ACTIVITY_ID + item.STATUS">
                                                            <td>{{ item.ACTIVITY_ID }}</td>
                                                            <td>{{ item.STATUS }}</td>
                                                            <td>{{ item.BANKED_UNITS }}</td>
                                                            <td>{{ item.BANK_CATEGORY_ID }}</td>
                                                            <td class="text-center">{{ item.DATE_OF_BANKING }}</td>
                                                        </tr>
                                                    </tbody>
                                                </table>
                                            </div>
                                        </div>
                                    </div>
                                    <div v-else class="listing text-danger text-center border-0">
                                        {{ $t("NorecordfoundText") }}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    </transition>
    <VerticalFlowChartModel v-if="modelopen" :modelDetail="modelDetail"></VerticalFlowChartModel>

</section>
</template>

<script>
import {
    locateBillingService
} from "../../../services/locateBillingService";
import Schema from "./TicketLogSchema";
import VerticalFlowChartModel from '../../../components/Modals/VerticalFlowChartModel.vue';

export default {
    name: "TicketLog",
    components: {
        VerticalFlowChartModel
    },
    data() {
        return {
            isLoading: false,
            PageSize: 10,
            PageNumber: 1,
            isPaged: 1,
            TotalPages: 0,
            widgets: [],
            LegendArray: [],
            SortBy: '',
            SortExp: '',
            CurrentPage: 1,
            TotalRecords: 0,
            ListingData: [],
            Buttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            Headers: Schema.headerSchema,
            searchCondition: "",
            selectedClientId: null,
            expandedRows: {},
            childRows: {},
            IsViewTicket: false,
            TicketContent: '',
            TicketNumber: '',
            TransmissionId: '',
            ActivityId: '',
            activeTab: 'OldProcessLog',
            processLogData: null,
            newProcessLogData: [],
            IsChildData: false,
            TicketActivityContent: [],
            ConfigurationId: '',
            modelopen: false
        }
    },
    created: function () {
        var vm = this;
        vm.Headers.forEach(element => {
            element.DISPLAY_NAME = vm.$t(element.DISPLAY_NAME);
        });
        if (vm.leftSearchFields ?.length > 0) {
            vm.leftSearchFields[1].onChange = vm.onSelectionChange;
        }
        Schema.bindleftCommonSearchdropdown(this);
        vm.fetchclientlist();
        //vm.FetchTicketLog();
    },
    methods: {
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                if (value.searchConditionJson != '') {
                    this.searchCondition = value.searchConditionJson;
                } else {
                    this.searchCondition = value.searchCondition || "";
                }
            } else {
                this.searchCondition = "";
            }
            if (this.searchCondition) {
                this.searchCondition = this.searchCondition.replace(/WorkDate=([^&]+)/, function (_, val) {
                    let [from, to] = val.split("|");
                    return `workDateFrom=${from}&workDateTo=${to}`;
                });
                this.searchCondition = this.searchCondition.replace(/ProcessDate=([^&]+)/, function (_, val) {
                    let [from, to] = val.split("|");
                    return `processDateFrom=${from}&processDateTo=${to}`;
                });
            }
            this.FetchTicketLog();
        },
        fetchclientlist: function () {
            var vm = this;
            vm.isLoading = true;
            var url = `reqFrom=CUSTOM`;
            locateBillingService.getClientList(url).then(response => {
                vm.isLoading = false;
                vm.clientList = response.data.items;
                vm.leftSearchFields.find(f => f.fieldName === 'ClientName').listOptions = vm.clientList;
                vm.groupedUtilities = vm.clientList;
            }).catch(error => {
                vm.isLoading = false;
                vm.clientList = [];
                vm.leftSearchFields.find(f => f.fieldName === 'ClientName').listOptions = [];
                console.error('Error fetching client list:', error);
            }).finally(() => {
                vm.isLoading = false;
            });
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = {
                    ...actionItem
                };
                switch (action.title) {
                    case 'TicketImage':
                        action.isVisible = true;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchTicketLog();
        },
        actionButtonClick: function (callbackfunction, item) {
            switch (callbackfunction) {
                case 'ViewTicketImage':
                    this.viewTicketImage(item);
                    break;
            }
        },
        commonHeaderButtonClick: function (event) {
            // Handle header button clicks if needed
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            if (this.SortExp == "") {
                this.SortExp = "ASC";
            } else if (this.SortExp == "ASC") {
                this.SortExp = "DESC";
            } else {
                this.SortExp = "ASC";
            }
            this.FetchTicketLog();
        },
        FetchTicketLog: function () {
            var vm = this;
            vm.isLoading = true;
            var url = `pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}`;

            // Add search condition if available
            if (vm.searchCondition) {
                url += `&${vm.searchCondition}`;
            }

            locateBillingService.GetTicketLog(url).then((response) => {
                vm.isLoading = false;
                if (response.data.result) {
                    vm.ListingData = JSON.parse(response.data.result)
                }
                if (vm.ListingData && vm.ListingData.length > 0) {
                    vm.ListingData.forEach((item) => {
                        if (item.WORK_DATE) {
                            item.WORK_DATE = new Date(item.WORK_DATE);
                        }
                        if (item.PROCESS_DATE) {
                            item.PROCESS_DATE = new Date(item.PROCESS_DATE);
                        }
                    });
                    vm.TotalRecords = vm.ListingData[0].TOTAL_RECORDS || vm.ListingData[0].TOTALRECORDS || 0;
                    vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                    vm.CurrentPage = vm.PageNumber;
                } else {
                    vm.ListingData = [];
                    vm.TotalRecords = 0;
                }
            }).catch((error) => {
                vm.isLoading = false;
                vm.ListingData = [];
                vm.TotalRecords = 0;
                console.error('Error fetching ticket log:', error);
            });

            // Placeholder - remove when API is ready
            setTimeout(() => {
                vm.isLoading = false;
                vm.ListingData = [];
                vm.TotalRecords = 0;
            }, 100);
        },
        formatDate(dateString) {
            if (!dateString) return '';
            if (dateString instanceof Date) {
                return dateString.toLocaleDateString();
            }
            const date = new Date(dateString);
            return date.toLocaleDateString();
        },
        toggleExpandRow: async function (item, event) {
            var vm = this;
            vm.TicketNumber = item.TICKETNUMBER;
            vm.ActivityId = item.ACTIVITY_ID;
            vm.IsChildData = !vm.IsChildData;
            var url = `TicketNumber=${vm.TicketNumber}&ActivityId=${vm.ActivityId}&workDateFrom=&workDateTo=`
            await locateBillingService.ChildTicketLog(url).then((response) => {
                if (response.data.length > 0) {
                    vm.TicketActivityContent = response.data;
                    vm.processLogData = response.data.find(x => x.TYPE === 'PROCESSLOG') || null;
                }
            });
            // Load new process log data
            var urlNew = `ticketNumber=${vm.TicketNumber}&activityId=${vm.ActivityId}&workDateFrom=&workDateTo=`;
            await locateBillingService.ChildTicketLogNew(urlNew).then((response) => {
                if (response.data && response.data.length > 0) {
                    vm.newProcessLogData = response.data;
                }
            });
        },
        CloseChildRow: function () {
            var vueObj = this;
            vueObj.activeTab = 'OldProcessLog';
            vueObj.IsChildData = false;
        },
        getWorkDateFrom() {
            var workDateField = this.leftSearchFields.find(f => f.fieldName === 'WorkDate');
            return workDateField && workDateField.value ? workDateField.value.from : '';
        },
        getWorkDateTo() {
            var workDateField = this.leftSearchFields.find(f => f.fieldName === 'WorkDate');
            return workDateField && workDateField.value ? workDateField.value.to : '';
        },
        viewTicketImage: async function (item) {
            var vm = this;
            vm.TicketNumber = item.TICKETNUMBER;
            vm.TransmissionId = item.TRANSMISSION_ID;
            vm.IsViewTicket = !vm.IsViewTicket;
            var url = `TicketNumber=${vm.TicketNumber}&TransmissionId=${vm.TransmissionId}`
            await locateBillingService.ViewTicketImage(url).then((response) => {
                if (response.data.length > 0) {
                    vm.TicketContent = response.data[0].RAW_IMAGE;
                }
            });
        },
        CloseTemplate: function () {
            var vueObj = this;
            vueObj.IsViewTicket = false;
        },
        viewBillingFlowchart(data) {
            var vm = this;
            if (data.CONFIGURATION_ID > 0) {
                vm.modelDetail = data;
                vm.modelopen = true;
            }
        },
        onSelectionChange(value, x) {
            // Load utilities when client selection changes
            var vm = this;
            vm.isLoading = true;
            vm.selectedClientId = x;
            var url = `clientId=${x}&configId=&reqFrom=&clientIds=`;
            locateBillingService.getUtilityList(url).then(response => {
                vm.isLoading = false;
                let SelectedUtilityList = [];
                var UtilityList = response.data;
                if (UtilityList != null) {
                    UtilityList.forEach(item => {
                        let obj = {
                            description: null,
                            displayValue: item.UTILITY_NAME,
                            groupText: null,
                            groupValue: null,
                            metaData: null,
                            text: item.UTILITY_NAME,
                            value: item.UTILITY_ID
                        };
                        SelectedUtilityList.push(obj);
                    });
                    vm.leftSearchFields.find(f => f.fieldName === 'UtilityName').listOptions = SelectedUtilityList;
                }
            }).catch(error => {
                vm.isLoading = false;
                vm.UtilityList = [];
                vm.leftSearchFields.find(f => f.fieldName === 'UtilityName').listOptions = [];
            }).finally(() => {
                vm.isLoading = false;
            });
        }
    }
}
</script>

