<template lang="en">
<section>
    <loader :is-visible="isLoading"></loader>

    <div class="col-md-12 p-0 mt-2">
        <div class="ticketsmallfix theme-primary partition p-actions-expand " style="overflow: visible;">
            <span class="p-name text-white">{{ $t('BulkMove') }}</span>
            <span class="p-actions float-right">
                <!-- Bulk Move Button -->
                <a title="Bulk Move" href="javascript:;" class="p-action-btn text-white" @click="bulkMoveAction">
                    <em class="fa fa-arrows"></em><br /><span>{{ $t('BulkMove') }}</span>
                </a>

                <!-- Delete Button -->
                <a title="Delete" href="javascript:;" class="p-action-btn text-white" @click="deleteAction">
                    <em class="fa fa-trash"></em><br /><span>{{ $t('Delete') }}</span>
                </a>

                <!-- View Queue List Button -->
                <a title="View Queue List" href="javascript:;" class="p-action-btn text-white" @click="viewQueueListAction">
                    <em class="fa fa-eye"></em><br /><span>{{ $t('ViewQueueList') }}</span>
                </a>
            </span>
        </div>
    </div>

<div class="border p-3 float-left w-100">
    <dynamic-form ref="dynamicform" lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit" :mandatorytext='MandatoryString'>
        <template v-slot:tgslot-SampleFile="{ data }">
              <div class="col-lg-12 col-xl-12 p-0">
                    <div class="form-group" id="divFiles">                       
                            <div class="input-group mb-3">
                                <div class="form-control">
                                    <span class="text-truncate">Download BulkMoveAdvanceSampleFile.xls</span>
                                </div>
                                <div class="input-group-append">
                                    <a href="https://cogniterblob.blob.core.windows.net/cg-usic-llc-310896/Upload/UserProfileImage/0f0cf000-22f8-40d0-ab1a-34fbcb382d96.xlsx" class="input-group-text" title="Download" checkprivilege="no">
                                        <i class="fa fa-download action-icon"></i>
                                    </a>
                                </div>                           
                        </div>
                    </div>
                </div>
                
        </template>
    </dynamic-form>
    <div class="heading-border mt-3">
       <h5 class="h5"><span>{{ $t('Ticket') }}</span></h5>
    </div>
    <div class="row mb-2 mt-2">
        <div class="col-12 mb-2">
            <div class="d-inline-block mr-2">
                <button type="button" class="btn btn-success text-white" @click="selectAllLineItems">
                    <i class="fa fa-check pr-2" style="width:20px;"></i>{{ $t('SelectAllLineItems') }}
                </button>
            </div>
            <div class="d-inline-block">
                <span class="legend-upcoming colorboxsquare"></span>{{ $t('InvoiceSentMessageOnBulkMove') }}
            </div>
        </div>
    </div>
    <tg-list 
    ref="tgList" 
    :listType="['List']" 
    :IsShowAction="false"
    :showCheckBox="true"
    ModuleName="BillingValidationData"
    SubModuleCode="BulkMove"
    IdentityColumn="COLUMN_ID"
    :HeaderText="$t('BulkMove')"
    :ListData="ListingData" 
    :HeaderData="Headers"
    :widgets="widgets"
    :HeaderButtons="[]" 
    :TotalRecords="TotalRecords"
    :RenderRowActionMethod="renderActions"
    :ListDataCallBackFunction="FetchBulkMoveRecords"
    :SortExp="SortExp"
    :SortBy="SortBy"
    :NorecordfoundText="$t('NoRecordfound')"
    v-on:PagerButtonClick="pagerMethod"
    v-on:HeaderButtonClick="commonHeaderButtonClick" 
    v-on:SortdataButtonClick="sortdata"
    :LegendArray="LegendArray"
    v-on:LeftsearchButtonClick="leftCommonSearch"
    v-on:ActionButtonClick="actionButtonClick"
    :SearchFields="false">
        <template v-slot:slotdata="{ data }">
            <template v-if="data.column.COLUMN_NAME == 'COST'">
                <span class="textalignright paddingright5">{{ formatCurrency(data.row.COST) }}</span>
            </template>
            <template v-if="data.column.COLUMN_NAME == 'DATEOFINV'">
                <span v-if="data.row.DATEOFINV" :title="data.row.DATEOFINV">
                    {{ formatDate(data.row.DATEOFINV) }}
                </span>
                <span v-else>--</span>
            </template>
            <template v-if="data.column.COLUMN_NAME == 'DATEOFWORK'">
                <span v-if="data.row.DATEOFWORK" :title="data.row.DATEOFWORK">
                    {{ formatDate(data.row.DATEOFWORK) }}
                </span>
                <span v-else>--</span>
            </template>
        </template>
    </tg-list>
</div>
    <ChangeClientAndUtility v-if="IsChangeLineItem" :lineItemIds="ColumnIds" :ticketNumberList="SelectedTicketNumbers" :activityIdList="SelectedActivityIds" :requestFrom="requestFrom" :oldClientList="selectedClientId" :oldUtilityList="SelectedUtilityList" :workDateFrom="WorkDateFrom" :workDateTo="WorkDateTo" :BulkMoveBatchId="BulkMoveBatchId"></ChangeClientAndUtility>
    <BulkMoveQueue v-if="IsViewQueueList" :ReqFrom="ReqFrom"></BulkMoveQueue>
</section>
</template>

<script>
import {
    locateBillingService
} from "../../../services/locateBillingService";
import Schema from "./BulkMoveSchema.js";
import ChangeClientAndUtility from "../../BillingValidationData/components/ChangeClientAndUtility.vue";
import BulkMoveQueue from "../../BillingValidationData/components/BulkMoveQueue.vue";

export default {
    name: "BulkMove",
    components: {
        ChangeClientAndUtility,
        BulkMoveQueue
    },
    data() {
        return {
            isLoading: false,
            PageSize: 10,
            PageNumber: 1,
            isPaged: 1,
            TotalPages: 0,
            widgets: [],
            LegendArray: [],
            SortBy: '',
            SortExp: '',
            CurrentPage: 1,
            TotalRecords: 0,
            ListingData: [],
            fileData: [],
            Buttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            Headers: Schema.headerSchema,
            searchCondition: "",
            selectedClientId: null,
            IsChangeLineItem: false,
            SelectedTicketNumbers: '',
            SelectedActivityIds: '',
            ColumnIds: '',
            requestFrom: 'bmfile',
            ReqFrom: '',
            WorkDateFrom: '',
            WorkDateTo: '',
            BulkMoveBatchId: '',
            IsViewQueueList: false,
            MandatoryString:'Fields marked with an asterisk (*) are mandatory.',
            FormSchema: [{
                layoutType: "triple",
                Data: [{
                        astype: "SearchableSelectField",
                        label: this.$t('CONTRACTNAME'),
                        name: "ContractName",
                        value: "",
                        placeholder: "",
                        visibility: true,
                        isSearch: false,
                        actionButtons: {
                            selectAll: true,
                            active: true,
                            inactive: true,
                            clear: true
                        },
                        config: {
                            options: [],
                            isMultiple: false,
                            groupFields: ['groupText'],
                            minSearchLength: 0,
                            statusFieldConfig: {
                                fieldName: 'status',
                                activeValues: ['Active', 'active'],
                                inactiveValues: ['Inactive', 'inactive'],
                                activeColor: '#28a745',
                                inactiveColor: '#dc3545'
                            },
                            onChange: this.onSelectionChange
                        },
                        validationRules: {
                            "required": true
                        },
                    },
                    {
                        astype: "SearchableSelectField",
                        label: this.$t('UtilityName'),
                        name: "UtilityName",
                        value: "",
                        placeholder: "",
                        visibility: true,
                        isSearch: false,
                        groupFields: ['groupText'],
                        actionButtons: {
                            selectAll: true,
                            active: true,
                            inactive: true,
                            clear: true
                        },
                        config: {
                            options: [],
                            isMultiple: true,
                            minSearchLength: 0,
                            statusFieldConfig: {
                                fieldName: 'status',
                                activeValues: ['Active', 'active'],
                                inactiveValues: ['Inactive', 'inactive'],
                                activeColor: '#28a745',
                                inactiveColor: '#dc3545'
                            },
                        },
                        validationRules: {
                            "required": true
                        },
                    },
                    {
                        astype: "NormalFileField",
                        label: this.$t('Image'),
                        name: "Attachment",
                        placeholder: "",
                         validationRules: {
                            "required": true
                        },
                        FieldIndex: 0,
                        config: {
                            maxSize: 5,
                            showSelectedFiles: [],
                            accept: [
                            { fileType: 'xlsx', iconClass: 'fa-file-excel-o' },
                            { fileType: 'xls',  iconClass: 'fa-file-excel-o' },
                            { fileType: 'csv',  iconClass: 'fa-file-text-o' }
                            ],
                            onDeleteSelectedFiles: this.onDeleteSelectedFiles,
                            onChange: this.GetBase64String,
                            showAddIcon: false,
                            onAddButtonClick: '',
                            multiple: false
                        },
                    },
                    {
                        astype: "DateField",
                        label: this.$t('WorkDateFrom'),
                        name: "WorkDateFrom",
                        value: "",
                        placeholder: "",
                        config: {
                            format: "MM/DD/YYYY",
                            maxDate: false
                        },
                        validationRules: {
                            "required": true
                        },
                    },
                    {
                        astype: "DateField",
                        label: this.$t('WorkDateTo'),
                        name: "WorkDateto",
                        value: new Date().toLocaleDateString(),
                        placeholder: "",
                        config: {
                            format: "MM/DD/YYYY",
                            maxDate: false
                        },
                        validationRules: {
                            "required": true
                        },
                    },
                    {
                        astype: "DateField",
                        label: this.$t('ProcessDateFrom'),
                        name: "ProcessDateFrom",
                        value: "",
                        placeholder: "",
                        config: {
                            format: "MM/DD/YYYY",
                            maxDate: false
                        },
                        validationRules: {
                            "required": false
                        },
                    },
                    {
                        astype: "DateField",
                        label: this.$t('ProcessDateTo'),
                        name: "ProcessDateto",
                        value: "",
                        placeholder: "",
                        config: {
                            format: "MM/DD/YYYY",
                            maxDate: false
                        },
                        validationRules: {
                            "required": false
                        },
                    },
                    {
                        astype: "SlotField",
                        label: this.$t('SampleFile'),
                        name: 'SampleFile',
                        value: "",
                        config: {
                            options: []
                        },
                        placeholder: this.$t('Select'),
                        validationRules: {
                            "required": false,
                            max: 100
                        },
                    },

                ],
            }, ],
            buttons: [{
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-search pr-2'></i>Search",
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-refresh pr-2'></i>Clear",
                    onClick: this.Clear,
                },
            ],
            SelectedUtilityList: ''
        };
    },
    created: function () {
        var vm = this;
        vm.Headers.forEach(element => {
            element.DISPLAY_NAME = vm.$t(element.DISPLAY_NAME);
        });
        vm.fetchclientlist();
        //vm.FetchBulkMoveRecords();
    },
    methods: {
        leftCommonSearch: function (value) {
            var vm = this;
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                if (value.searchConditionJson != '') {
                    this.searchCondition = value.searchConditionJson;
                } else {
                    this.searchCondition = value.searchCondition || "";
                    // this.leftSearchFields.find(f => f.fieldName === 'UtilityName').listOptions = [];

                }
            } else {
                this.searchCondition = "";
            }
            if (this.searchCondition) {
                this.searchCondition = this.searchCondition.replace(/WorkDate=([^&]+)/, function (_, val) {
                    let [from, to] = val.split("|");
                    vm.WorkDateFrom = from;
                    vm.WorkDateTo = to;
                    return `WorkDateFrom=${from}&WorkDateTo=${to}`;
                });
                this.searchCondition = this.searchCondition.replace(/ProcessDate=([^&]+)/, function (_, val) {
                    let [from, to] = val.split("|");
                    return `ProcessDateFrom=${from}&ProcessDateTo=${to}`;
                });
            }
            this.FetchBulkMoveRecords();
        },
        renderActions: function () {
            return [];
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchBulkMoveRecords();
        },
        actionButtonClick: function () {
            // No row actions for bulk move listing
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "bulkMoveAction":
                    this.bulkMoveAction();
                    break;
                case "deleteAction":
                    this.deleteAction();
                    break;
                case "viewQueueListAction":
                    this.viewQueueListAction();
                    break;
            }
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            if (this.SortExp == "") {
                this.SortExp = "ASC";
            } else if (this.SortExp == "ASC") {
                this.SortExp = "DESC";
            } else {
                this.SortExp = "ASC";
            }
            this.FetchBulkMoveRecords();
        },
        GetBase64String: function (evt) {
            try {
                var vm = this;
                vm.fileData = [];
                var files = evt.target.files; // FileList object
                vm.attachmentName = files[0].name;
                for (var i = 0; i < files.length; i++) {
                    var f = files[i];
                    var reader = new FileReader();
                    // Closure to capture the file information.
                    reader.onload = (function (theFile) {
                        return function (e) {
                            var binaryData = e.target.result;
                            //Converting Binary Data to base 64
                            var base64StringEncoded = window.btoa(binaryData);
                            vm.base64String = base64StringEncoded;
                            //showing file converted to base64

                            var fileName = f.name;
                            //vm.fileName = fileName;
                            var n = fileName.lastIndexOf(".");
                            var extensionName = fileName.substring(n + 1, fileName.length);

                            //vm.fileExt += "." + extensionName;
                            var attachemnetMime = '{"ContentType":"' + f.type + '","Size":"' + f.size + '","Extension":"' + extensionName + '"}';
                            var fileSize = f.size;
                            //vm.fileSize = fileSize;
                            var fileObj = {
                                attachement_mime: attachemnetMime,
                                fileName: fileName,
                                userId: vm.GetUserInfo.ID,
                                companyId: vm.GetUserInfo.CompanyId,
                                id: 0,
                                moduleName: "initialsetup",
                                subModuleCode: "",
                                containerName: vm.GetUserInfo.ContainerName,
                                base64String: base64StringEncoded,
                                attachement_path: ''
                            };

                            vm.fileData.push(fileObj);
                        };
                    })(f);
                    // Read in the image file as a data URL.
                    reader.readAsBinaryString(f);
                }
            }
            catch (ex) {
            }
        },
         formatDate(dateStr) {
            let dt = new Date(dateStr);
            let month = dt.getMonth() + 1;
            let day = dt.getDate();
            let year = dt.getFullYear();
            return `${month}/${day}/${year}`;
        },
        async onSubmit(formfields, val) {
            var vm = this;
            vm.isLoading =true;
            let WorkDateFrom = vm.formatDate(formfields.WorkDateFrom);
            let WorkDateTo = vm.formatDate(formfields.WorkDateto);
            let ProcessDateFrom = vm.formatDate(formfields.ProcessDateFrom);
            let ProcessDateTo = vm.formatDate(formfields.ProcessDateto);
             var formData = JSON.stringify(vm.fileData);
            await locateBillingService.UploadFile(formData).then(function (response) {
                if (response.data && response.data.length > 0) {
                    if (response.data[0].Name.length > 0) {
                        vm.filePath = response.data[0].Name;
                    }
                }
            })
            let u = formfields.UtilityName;
            let SelectedUtilities = (Array.isArray(u) && u.length > 1)? u.join(',') : (Array.isArray(u) ? u[0] : u);
            vm.SelectedUtilityList=SelectedUtilities;
            var filePath = vm.filePath;
            var url = `pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&utilityName=${SelectedUtilities}&clients=${formfields.ContractName}&workDateFrom=${WorkDateFrom}&workDateTo=${WorkDateTo}
            &processDateFrom=${ProcessDateFrom}&processDateTo=${ProcessDateTo}` +
                `&filepath=${encodeURIComponent(filePath)}` +
                `&filename=${encodeURIComponent('BulkMove_SampleFile.xlsx')}` +
                `&fileNewname=${encodeURIComponent('758499944.xlsx')}`;

            if (vm.searchCondition) {
                url += `&${vm.searchCondition}`;
            }
            // TODO: Replace with actual API call
            locateBillingService.GetBulkMoveListing(url).then((response) => {
                vm.isLoading = false;
                if (response.data.result) {
                    vm.ListingData = JSON.parse(response.data.result);
                    vm.BulkMoveBatchId = response.data.bulkMoveBatchId;
                }
                if (vm.ListingData && vm.ListingData.length > 0) {
                    vm.ListingData.forEach((item) => {
                        if (item.DATEOFINV) {
                            item.DATEOFINV = new Date(item.DATEOFINV);
                        }
                        if (item.DATEOFWORK) {
                            item.DATEOFWORK = new Date(item.DATEOFWORK);
                        }
                    });
                    vm.TotalRecords = vm.ListingData[0].TOTAL_RECORDS || vm.ListingData[0].TOTALRECORDS || 0;
                    vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                    vm.CurrentPage = vm.PageNumber;
                } else {
                    vm.ListingData = [];
                    vm.TotalRecords = 0;
                }
            })

        },
        FetchBulkMoveRecords: function () {
            var vm = this;
            vm.isLoading = true;
            var filePath = vm.filePath;
            var url = `pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&utilityName=${SelectedUtilities}&clients=${formfields.ContractName}&workDateFrom=${WorkDateFrom}&workDateTo=${WorkDateTo}
            &processDateFrom=${ProcessDateFrom}&processDateTo=${ProcessDateTo}` +
                `&filepath=${encodeURIComponent(filePath)}` +
                `&filename=${encodeURIComponent('BulkMove_SampleFile.xlsx')}` +
                `&fileNewname=${encodeURIComponent('758499944.xlsx')}`;

            if (vm.searchCondition) {
                url += `&${vm.searchCondition}`;
            }
            // TODO: Replace with actual API call
            locateBillingService.GetBulkMoveListing(url).then((response) => {
                 vm.isLoading = false;
                if(response.data.result){
                    vm.ListingData=JSON.parse(response.data.result);
                    vm.BulkMoveBatchId=response.data.bulkMoveBatchId;
                }
                if (vm.ListingData  && vm.ListingData.length > 0) {
                    vm.ListingData.forEach((item) => {
                        if (item.DATEOFINV) {
                            item.DATEOFINV = new Date(item.DATEOFINV);
                        }
                        if (item.DATEOFWORK) {
                            item.DATEOFWORK = new Date(item.DATEOFWORK);
                        }
                    });
                    vm.TotalRecords = vm.ListingData[0].TOTAL_RECORDS || vm.ListingData[0].TOTALRECORDS || 0;
                    vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                    vm.CurrentPage = vm.PageNumber;
                } else {
                    vm.ListingData = [];
                    vm.TotalRecords = 0;
                }
            }).catch((error) => {
                vm.isLoading = false;
                vm.ListingData = [];
                vm.TotalRecords = 0;
                console.error('Error fetching flag records:', error);
            });
            
            // Placeholder - remove when API is ready
            setTimeout(() => {
                vm.isLoading = false;
                vm.ListingData = [];
                vm.TotalRecords = 0;
            }, 100);
        },
        formatDate(dateString) {
            if (!dateString) return '';
            if (dateString instanceof Date) {
                return dateString.toLocaleDateString();
            }
            const date = new Date(dateString);
            return date.toLocaleDateString();
        },
        formatCurrency(value) {
            if (!value && value !== 0) return '0.00';
            return parseFloat(value).toFixed(2);
        },
        selectAllLineItems() {
            $('.chkItems').not("[id^='chkAll_0']").prop('checked', true);
        },
        fetchclientlist: function () {
            var vm = this;
            vm.isLoading = true;
            var url = `reqFrom=CUSTOM`;
            locateBillingService.getClientList(url).then(response => {
                vm.isLoading = false;
                vm.clientList = response.data.items;
                vm.$refs.dynamicform.updateProperty("ContractName", "config.options", vm.clientList);
                vm.$refs.dynamicform.UpdateKeyValue();
            }).catch(error => {
                vm.isLoading = false;
                vm.clientList = []; 
                vm.$refs.dynamicform.updateProperty("ContractName", "config.options", vm.clientList);
                vm.$refs.dynamicform.UpdateKeyValue();
                console.error('Error fetching client list:', error);
            }).finally(() => {
                vm.isLoading = false;
            });
        },
        Close: function () {
            this.$parent.IsChangeLineItem = false;
        },
        onSelectionChange(value, x) {
            var vm = this;
            vm.isLoading = true;
            vm.selectedClientId = x;
            var url = `clientId=${x}&configId=&reqFrom=&clientIds=`;
            locateBillingService.getUtilityList(url).then(response => {
                vm.isLoading = false;
                let SelectedUtilityList = [];
                var UtilityList = response.data;
                if (UtilityList != null) {
                    UtilityList.forEach(item => {
                        let obj = {
                            description: null,
                            displayValue: item.UTILITY_NAME,
                            groupText: null,
                            groupValue: null,
                            metaData: null,
                            text: item.UTILITY_NAME,
                            value: item.UTILITY_ID
                        };
                        SelectedUtilityList.push(obj);
                    });
                     vm.$refs.dynamicform.updateProperty("UtilityName", "config.options", SelectedUtilityList);
                }
            }).catch(error => {
                vm.isLoading = false;
                vm.UtilityList = [];
                  vm.$refs.dynamicform.updateProperty("UtilityName", "config.options", []);
            }).finally(() => {
                vm.isLoading = false;
            });
        },
        bulkMoveAction: function (event) {
            var vm = this;
            var selected = [];
            $(".chkItems:checkbox:checked").not("[id^='chkAll_0']").each(function () {
                selected.push($(this).val());
            });
            if (selected.length === 0) {
                vm.ShowAlert(vm.$t("PleaseSelectAnyRecordsForFurther"), "warning", true, vm.$t("Alert"));
                return;
            }
            vm.ColumnIds = selected.filter(x => x != null && x !== "").join(',');
            vm.FilteredList = vm.ListingData.filter(x => selected.includes(String(x.COLUMN_ID)));
            vm.SelectedTicketNumbers = (vm.FilteredList.length > 0) ? vm.FilteredList.map(o => o.TICKETNUMBER).join(',') : "";
            vm.SelectedActivityIds = (vm.FilteredList.length > 0) ? vm.FilteredList.map(o => o.ACTIVITY_ID).join(',') : "";
            vm.IsChangeLineItem = !vm.IsChangeLineItem;
        },
        deleteAction() {
            var vm = this;
            var val = "";
            var selected = [];
            $(".chkItems:checkbox:checked").not("[id^='chkAll_0']").each(function () {
                selected.push($(this).val());
            });

            if (selected.length === 0) {
                vm.ShowAlert(vm.$t("PleaseSelectAnyRecordsForFurther"), "warning", true, vm.$t("Alert"));
                return;
            }
            vm.ColumnIds = selected.filter(x => x != null && x !== "").join(',');
            vm.FilteredList = vm.ListingData.filter(x => selected.includes(String(x.COLUMN_ID)));
            vm.SelectedTicketNumbers = (vm.FilteredList.length > 0) ? vm.FilteredList.map(o => o.TICKETNUMBER).join(',') : "";
            vm.SelectedActivityIds = (vm.FilteredList.length > 0) ? vm.FilteredList.map(o => o.ACTIVITY_ID).join(',') : "";
            if (selected.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, vm.$t("BulkMove"), function () {
                    var postJSON = {
                        utilityName: 0,
                        clientId: vm.selectedClientId,
                        sbaId: "",
                        divisionId: "",
                        lineItemIds: vm.ColumnIds,
                        reqFrom: "bmDfile",
                        ticketNumber: vm.SelectedTicketNumbers,
                        activityId: vm.SelectedActivityIds,
                        oldClient: vm.selectedClientId,
                        oldUtility: vm.SelectedUtilityList,
                        workDateFrom: vm.WorkDateFrom,
                        workDateTo: vm.WorkDateTo,
                        allInvoice: "",
                        bulkMoveBatchId: vm.BulkMoveBatchId
                    };
                    locateBillingService.UpdateContractAndUtility(postJSON).then(function (response) {
                        if (response.data.msg == "success") {
                            vm.ShowAlert(vm.$t('RecordUpdatedSuccessfully'), "success", true, vm.$t("Alert"));
                            vm.ListingData = vm.ListingData.filter(item => !vm.FilteredList.some(filtered => filtered.COLUMN_ID === item.COLUMN_ID));
                            vm.TotalRecords -= vm.FilteredList.length;
                            vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                        } else {
                            vm.ShowAlert(response.data.msg, "failure", true, vm.$t("Alert"));
                        }
                    }).catch(function () {
                        vm.ShowAlert(vm.$t("ErrorWhileProcessingRequest"), "failure", true, vm.$t("Alert"));
                    });
                });
            } else {
                vm.ShowAlert(vm.$t("PleaseSelectAnyRecordsForFurther"), "warning", true, vm.$t("Alert"));
            }
        },
        viewQueueListAction() {
            var vm = this;
            vm.IsViewQueueList = !vm.IsViewQueueList;
        },
        Clear: function () {
            var vm = this;
            // Reset form field values to initial state
            vm.FormSchema[0].Data.forEach(field => {
                if (field.name === 'WorkDateto') {
                    field.value = new Date().toLocaleDateString();
                } else {
                    field.value = "";
                }
            });
            // Clear file data
            vm.fileData = [];
            // Clear selected client and utility options
            vm.selectedClientId = null;
            vm.$refs.dynamicform.updateProperty("UtilityName", "config.options", []);
            // Update the dynamic form
            vm.$refs.dynamicform.UpdateKeyValue();
        }
    }
}
</script>

