<template>
    <div class="my-popups">
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered modal-xl">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full d-flex">
                        <h4 class="modal-title">{{ $t('QueueList') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>

                    <div class="modal-body">
                        <loader :is-visible="isLoading"></loader>

                        <tg-list
                            ref="tgList"
                            :listType="['List']"
                            :IsShowAction="false"
                            :showCheckBox="false"
                            ModuleName="BillingValidationData"
                            SubModuleCode="BulkMoveQueue"
                            IdentityColumn="QUEUE_ID"
                            :HeaderText="$t('BulkMoveQueue')"
                            :ListData="ListingData"
                            :HeaderData="Headers"
                            :widgets="widgets"
                            :HeaderButtons="Buttons"
                            :TotalRecords="TotalRecords"
                            :RenderRowActionMethod="renderActions"
                            :ListDataCallBackFunction="FetchBulkMoveQueueRecords"
                            :SortExp="SortExp"
                            :SortBy="SortBy"
                            :NorecordfoundText="$t('NoRecordfound')"
                            v-on:PagerButtonClick="pagerMethod"
                            v-on:HeaderButtonClick="commonHeaderButtonClick"
                            v-on:SortdataButtonClick="sortdata"
                            :LegendArray="LegendArray"
                            v-on:LeftsearchButtonClick="leftCommonSearch"
                            v-on:ActionButtonClick="actionButtonClick"
                            :SearchFields="leftSearchFields"
                            SearchPosition="top"
                        >
                            <template v-slot:slotdata="{ data }">
                                <template v-if="data.column.COLUMN_NAME == 'DATE_RANGE'">
                                    <span>
                                      {{ $options.filters.formatDate(data.row.FROM_DATE) }} -{{ $options.filters.formatDate(data.row.TO_DATE) }}  
                                    </span>
                                </template>
                                <template v-if="data.column.COLUMN_NAME == 'STATUS'">
                                    <span v-if="data.row.PROCESSING_STATUS == 1" class="bg-warning py-1 px-1 text-white w-100 d-block text-center">
                                        {{ $t('In_Progress') }}
                                    </span>
                                    <span v-else-if="data.row.STATUS_ID == 1003" class="bg-success py-1 px-1 text-white w-100 d-block text-center">
                                        {{ $t('Completed') }}
                                    </span>
                                    <span v-else class="bg-primary py-1 px-1 text-white w-100 d-block text-center">
                                        {{ $t('InQueue') }}
                                    </span>
                                </template>
                                <template v-if="data.column.COLUMN_NAME == 'CREATED_AT'">
                                    <span>
                                      {{ $options.filters.formatDate(data.row.CREATED_AT) }} 
                                    </span>
                                </template>
                            </template>
                        </tg-list>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { locateBillingService } from "../../../services/locateBillingService";
import Schema from "./BulkMoveQueueSchema";
import Loader from "../../../components/Common/Loader.vue";

export default {
    name: "BulkMoveQueue",
    props: {
        ReqFrom: {
            type: String,
            required: false
        }
    },
    components: { Loader },
    data() {
        return {
            isLoading: false,
            PageSize: 10,
            PageNumber: 1,
            isPaged: 1,
            TotalPages: 0,
            widgets: [],
            LegendArray: [],
            SortBy: '',
            SortExp: '',
            CurrentPage: 1,
            TotalRecords: 0,
            ListingData: [],
            Buttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            Headers: Schema.headerSchema,
            searchCondition: "",
            selectedClientId: null,
            selectedUtilityIds: [],
            selectedUserIds: [],
            requestedAt: null
        };
    },
    created: function () {
        var vm = this;
        vm.Headers.forEach(element => {
            element.DISPLAY_NAME = vm.$t(element.DISPLAY_NAME);
        });
        if (vm.leftSearchFields?.length > 0) {
            vm.leftSearchFields.find(f => f.fieldName === 'ClientName').onChange = vm.onClientChange;
        }
        Schema.bindleftCommonSearchdropdown(this);
        vm.FetchBulkMoveQueueRecords();
    },
    methods: {
        leftCommonSearch: function (value) {
            var vm = this;
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                if (value.searchConditionJson != '') {
                    this.searchCondition = value.searchConditionJson;
                } else {
                    this.searchCondition = value.searchCondition || "";
                }
            } else {
                this.searchCondition = "";
            }
            this.FetchBulkMoveQueueRecords();
        },
        renderActions: function () {
            return [];
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchBulkMoveQueueRecords();
        },
        actionButtonClick: function () {
            // No row actions for bulk move queue listing
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "searchAction":
                    this.searchAction();
                    break;
                case "clearAction":
                    this.clearAction();
                    break;
            }
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            if (this.SortExp == "") {
                this.SortExp = "ASC";
            } else if (this.SortExp == "ASC") {
                this.SortExp = "DESC";
            } else {
                this.SortExp = "ASC";
            }
            this.FetchBulkMoveQueueRecords();
        },
        FetchBulkMoveQueueRecords: function () {
            var vm = this;
            vm.isLoading = true;
            var url = `pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&advanceType=${vm.ReqFrom}`;

            if (vm.searchCondition) {
                url += `&${vm.searchCondition}`;
            }
            locateBillingService.GetBulkMoveQueueList(url).then((response) => {
                vm.isLoading = false;
                if (response.data.result) {
                    vm.ListingData = JSON.parse(response.data.result);
                    if (vm.ListingData && vm.ListingData.length > 0) {
                        vm.ListingData.forEach((item, index) => {
                            item.SERIAL_NO = (vm.PageNumber - 1) * vm.PageSize + index + 1;
                        });
                        vm.TotalRecords = vm.ListingData[0].TOTAL_RECORDS || vm.ListingData[0].TOTALRECORDS || 0;
                        vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                        vm.CurrentPage = vm.PageNumber;
                    } else {
                        vm.ListingData = [];
                        vm.TotalRecords = 0;
                    }
                } else {
                    vm.ListingData = [];
                    vm.TotalRecords = 0;
                }
            }).catch((error) => {
                vm.isLoading = false;
                vm.ListingData = [];
                vm.TotalRecords = 0;
                console.error('Error fetching bulk move queue records:', error);
            });
        },
        fetchClientList: function () {
            var vm = this;
            vm.isLoading = true;
            var url = `reqFrom=CUSTOM`;
            locateBillingService.getClientList(url).then(response => {
                vm.isLoading = false;
                vm.clientList = response.data.items;
                // Assuming grouped list, need to format accordingly
                vm.leftSearchFields.find(f => f.fieldName === 'ClientName').listOptions = vm.clientList;
            }).catch(error => {
                vm.isLoading = false;
                vm.clientList = [];
                vm.leftSearchFields.find(f => f.fieldName === 'ClientName').listOptions = [];
                console.error('Error fetching client list:', error);
            }).finally(() => {
                vm.isLoading = false;
            });
        },
        fetchUserList: function () {
            var vm = this;
            vm.isLoading = true;
            // Assuming API to get user list for bulk move
            var url = `isActive=1&moduleName=BulkMoveUsingFile`;
            locateBillingService.getUserList(url).then(response => {
                vm.isLoading = false;
                let RequestedUserList = [];
                var userList = response.data;
                if (userList != null) {
                    userList.forEach(item => {
                        let obj = {
                            description: null,
                            displayValue: item.UserName,
                            groupText: null,
                            groupValue: null,
                            metaData: null,
                            text: item.UserName,
                            value: item.UserId
                        };
                        RequestedUserList.push(obj);
                    });
                    vm.leftSearchFields.find(f => f.fieldName === 'RequestedBy').listOptions = RequestedUserList;
                }
            }).catch(error => {
                vm.isLoading = false;
                vm.leftSearchFields.find(f => f.fieldName === 'RequestedBy').listOptions = [];
                console.error('Error fetching user list:', error);
            }).finally(() => {
                vm.isLoading = false;
            });
        },
        onClientChange(value, x) {
            var vm = this;
            vm.selectedClientId = x;
            vm.fetchUtilityList(x);
        },
        fetchUtilityList: function (clientId) {
            if (!clientId) {
                this.leftSearchFields.find(f => f.fieldName === 'UtilityName').listOptions = [];
                return;
            }
            var vm = this;
            vm.isLoading = true;
            var url = `clientId=${clientId}&configId=&reqFrom=&clientIds=`;
            locateBillingService.getUtilityList(url).then(response => {
                vm.isLoading = false;
                let utilityList = [];
                var utilities = response.data;
                if (utilities != null) {
                    utilities.forEach(item => {
                        let obj = {
                            description: null,
                            displayValue: item.UTILITY_NAME,
                            groupText: null,
                            groupValue: null,
                            metaData: null,
                            text: item.UTILITY_NAME,
                            value: item.UTILITY_ID
                        };
                        utilityList.push(obj);
                    });
                    vm.leftSearchFields.find(f => f.fieldName === 'UtilityName').listOptions = utilityList;
                }
            }).catch(error => {
                vm.isLoading = false;
                vm.leftSearchFields.find(f => f.fieldName === 'UtilityName').listOptions = [];
                console.error('Error fetching utility list:', error);
            }).finally(() => {
                vm.isLoading = false;
            });
        },
        searchAction: function () {
            // Trigger search by calling FetchBulkMoveQueueRecords with current filters
            this.FetchBulkMoveQueueRecords();
        },
        clearAction: function () {
            // Clear all filters
            this.leftSearchFields.forEach(field => {
                if (field.fieldType === 'searchableselect') {
                    field.value = "";
                } else if (field.fieldType === 'date') {
                    // Clear date
                }
            });
            this.searchCondition = "";
            this.selectedClientId = null;
            this.selectedUtilityIds = [];
            this.selectedUserIds = [];
            this.requestedAt = null;
            // Clear utility list
            this.leftSearchFields.find(f => f.fieldName === 'UtilityName').listOptions = [];
            // Trigger fetch with cleared filters
            this.FetchBulkMoveQueueRecords();
        },
        Close: function () {
            this.$parent.IsViewQueueList = false;
        }
    }
}
</script>

<style scoped>
.my-popups .modal-dialog {
    max-width: 95%;
}
</style>