<template>
  
    <div class="tab-content-wrapper">
        <div class="theme-primary partition p-actions-expand mt-2">
           <span class="p-name text-white">{{ $t('PreviewInvoice') }}</span>
                        <!-- <RouterLink to="/BillingValidationData" title="" class="p-action-btn text-white float-right"
                            data-toggle-tooltip="tooltip" data-original-title="Back to list">
                            <i class="fa fa-angle-double-left pr-2"></i><br>{{ $t("BacktoList") }}
                        </RouterLink> -->
        </div>
        <div class="col-md-12 p-3">
            <dynamic-form ref="dynamicform" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit">
            </dynamic-form>
        </div>
    </div>
</template>

<script>
import { locateBillingService } from "../../../services/locateBillingService";

export default {
    name: "PreviewInvoice",
    data() {
        return {
            isLoading: false,
            FormSchema: [{
                layoutType: "triple",
                Data: [{
                    astype: "SearchableSelectField",
                    label: this.$t('CONTRACTNAME'),
                    name: "ContractName",
                    value: "",
                    placeholder: "",
                    isSearch: false,
                    actionButtons: { selectAll: true, active: true, inactive: true, clear: true },
                    config: {
                        options: [],
                        groupFields: ['groupText'],
                        isMultiple: false,
                        minSearchLength: 0,
                        statusFieldConfig: {
                            fieldName: 'status',
                            activeValues: ['Active', 'active'],
                            inactiveValues: ['Inactive', 'inactive'],
                            activeColor: '#28a745',
                            inactiveColor: '#dc3545'
                        },
                         onChange: this.onSelectionChange
                    },
                    validationRules: {
                        "required": true
                    },
                },
                {
                    astype: "SearchableSelectField",
                    label: this.$t('Division'),
                    name: "InvoiceGrouping",
                    value: "",
                    placeholder: "",
                    isSearch: false,
                    groupFields: [],
                     actionButtons: { selectAll: true, active: true, inactive: true, clear: true },
                    config: {
                        options: [],
                        isMultiple: true,
                        minSearchLength: 0,
                        statusFieldConfig: {
                            fieldName: 'status',
                            activeValues: ['Active', 'active'],
                            inactiveValues: ['Inactive', 'inactive'],
                            activeColor: '#28a745',
                            inactiveColor: '#dc3545'
                        },
                    },
                    validationRules: {
                        "required": true
                    },
                },
                {
                    astype: "RadioField",
                    label: this.$t('ReportType'),
                    name: "ReportType",
                    value: "Detailed",
                    config: {
                        options: [
                            { name: this.$t('InvoiceBackupReport'), value: 'Detailed' },
                            { name: this.$t('SummaryReport'), value: 'Summary' }
                        ]
                    },
                    validationRules: {
                        "required": false
                    },
                },
                {
                    astype: "DateField",
                    label: this.$t('ProcessDateFrom'),
                    name: "ProcessDateFrom",
                    value: '',
                    placeholder: "",
                    config: {
                        format: "MM/DD/YYYY",
                        maxDate: false
                    },
                    validationRules: { "required": true },
                },
                {
                    astype: "DateField",
                    label: this.$t('ProcessDateTo'),
                    name: "ProcessDateTo",
                    value: '',
                    placeholder: "",
                    config: {
                        format: "MM/DD/YYYY",
                        maxDate: false
                    },
                    validationRules: { "required": true },
                }],
            }],
            buttons: [{
                type: "submit",
                class: "btn btn-success",
                text: "<i class='fa fa-search pr-2'></i>Search",
            },
            {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Clear",
                    onClick: this.Close,
                },],
        }
    },
    created: async function () {
        await this.fetchClientList();
    },
    methods: {
        fetchClientList: function () {
            var vm = this;
            vm.isLoading = true;
            var url = `reqFrom=CUSTOM`;
            locateBillingService.getClientList(url).then(response => {
                vm.isLoading = false;
                vm.clientList = response.data.items;
                vm.$refs.dynamicform.updateProperty("ContractName", "config.options", vm.clientList);
                vm.$refs.dynamicform.UpdateKeyValue();
            }).catch(error => {
                vm.isLoading = false;
                vm.clientList = [];
                vm.$refs.dynamicform.updateProperty("ContractName", "config.options", vm.clientList);
                vm.$refs.dynamicform.UpdateKeyValue();
                console.error('Error fetching client list:', error);
            }).finally(() => {
                vm.isLoading = false;
            });
        },
         onSelectionChange(value,x) {
            var vm = this;
            vm.isLoading = true;
            var url = `clientId=${x}&utilityIds=`;
            locateBillingService.LoadClientDivisionList(url).then(response => {
                vm.isLoading = false;
                let SelectedDivisionList=[];
                    var DivisionList = response.data;
                    if (DivisionList != null) {
                        DivisionList.forEach(item => {
                            let obj = {
                                description: null,
                                displayValue: item.NOTE,
                                groupText: null,
                                groupValue: null,
                                metaData: null,
                                text: item.NOTE,
                                value: item.ID   
                            };
                            SelectedDivisionList.push(obj);
                        });
                vm.$refs.dynamicform.updateProperty("InvoiceGrouping", "config.options", SelectedDivisionList);
                vm.$refs.dynamicform.UpdateKeyValue();;
            }
            }).catch(error => {
                vm.isLoading = false;
                vm.SelectedDivisionList = []; 
                vm.$refs.dynamicform.updateProperty("InvoiceGrouping", "config.options", SelectedDivisionList);
                vm.$refs.dynamicform.UpdateKeyValue();
            }).finally(() => {
                vm.isLoading = false;
            });
        },
        onSubmit(formfields, val) {
            var vm = this;
            debugger
        }
    }
}
</script>

<style scoped>
.tab-content-wrapper {
    min-height: 400px;
}
</style>

